//
// Post Minimal
//
.post-minimal {
  $post-minimal-background: rgba($gray-800, .08);
  position: relative;
  padding: 22px 15px 22px 15px;
  margin-bottom: 15px;
  max-width: 270px;
  border-radius: 6px;
  text-align: left;
  background: $post-minimal-background;

  &::after {
    content: '';
    position: absolute;
    left: 20px;
    bottom: -15px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 15px 25px 0 0;
    border-color: $post-minimal-background transparent transparent transparent;
  }

  p {
    font-size: 15px;
    line-height: (24 / 15);
  }
}

.post-minimal__time {
  display: block;
  font-family: $font-family-sec;
  letter-spacing: .025em;
  color: $primary;
}

// Context styling
%context-dark {
  .post-minimal {
    $post-minimal-background: rgba($white, .08);
    background: $post-minimal-background;
    color: $white;

    &::after {
      border-color: $post-minimal-background transparent transparent transparent;
    }
  }
}

.post-minimal-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;

  > * + * {
    margin-top: 10px;
  }

  // Small <576px
  @include media-breakpoint-down(xs) {
    > * {
      max-width: 250px;

      &:nth-child(even) .post-minimal {
        &::after {
          left: auto;
          right: 20px;
          transform: scale(-1, 1);
        }
      }
    }
  }
}

// Medium ≥576px
@include media-breakpoint-up(sm) {
  .post-minimal-group {
    @include groupX(20px);

    > * {
      max-width: calc(50% - 20px);
    }

    > *:nth-child(1) {
      margin-top: 50px;
    }

    > *:nth-child(n + 2) {
      margin-top: 15px;
    }
  }
}

// Extra large ≥1200px
@include media-breakpoint-up(xl) {
  .post-minimal {
    p {
      font-size: 17px;
      line-height: (24 / 17);
    }
  }

  .post-minimal-group {
    @include groupX(50px);

    > * {
      max-width: calc(50% - 50px);
    }
  }
}

// 
// Post Classic
//
.post-classic {
  position: relative;
  z-index: 1;
  background: $white;
  box-shadow: $shadow-area-1;

  .quote-light {
    max-width: 280px;
  }

  a.quote-light {
    transform: scale3d(1, 1, 1);
    transition: .25s cubic-bezier(0, 0, 0.2, 1);

    &:hover {
      transform: scale3d(1.04, 1.04, 1.04);
    }
  }
}

.post-classic__media {
  position: relative;
  display: block;
  min-height: 190px;
  background: $white;

  .badge {
    position: absolute;
    top: 30px;
    left: 35px;
    z-index: 1;
  } 

  img {
    width: 100%;
  }
}

.post-classic__image-link {
  display: block;
  position: relative;
  overflow: hidden;
  min-height: inherit;
  background: $gray-800;

  img {
    min-height: inherit;
    width: auto;
    max-width: none;
    min-width: 100%;
    opacity: 1;
    will-change: transform;
    transform: scale3d(1, 1, 1);
    transition: .4s cubic-bezier(0, 0, 0.2, 1);
  }

  &:hover {
    img {
      opacity: .95;
      transform: scale3d(1.05, 1.05, 1.05);
    }
  }
}

.post-classic__carousel {
  .owl-prev,
  .owl-next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    color: $white;
    background: rgba($black, .38);
    transition: .25s cubic-bezier(0, 0, 0.2, 1);

    &:hover {
      color: $white;
      background: $primary;
    }
  }
}

.post-classic__main {
  padding: 15px 15px 20px;
}

.post-classic__meta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  @include spacing(20px, 8px);
}

.post-classic__time {
  display: block;
  letter-spacing: .05em;
  color: $primary;
}

* + .post-classic { margin-top: 35px; }
html * + .post-classic__carousel { margin-top: 0; }
* + .post-classic__meta { margin-top: 20px; }

@include media-breakpoint-up(md) {
  .post-classic__main {
    padding: 35px 20px 40px 35px;
  }

  * + .post-classic__meta { margin-top: 30px; }
}

@include media-breakpoint-between(lg, lg) {
  .post-classic {
    .badge {
      top: 20px;
      left: 20px;
    }
  }

  .post-classic__carousel {
    .owl-prev,
    .owl-next {
      width: 45px;
      height: 45px;
    }
  }

  .post-classic__main {
    padding: 25px 22px 30px 25px;
  }
}

// Post preview
.post-preview {
  display: flex;
  align-items: center;
  @include groupX(20px);
}

.post-preview__image-outer {
  position: relative;
  overflow: hidden;
  display: block;
  max-width: 85px;
  border-radius: 50%;
  flex-shrink: 0;

  &:hover {
  }
}

.post-preview__title {
  color: $headings-color;

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }
}

.post-preview__time {
  display: block;
  letter-spacing: .05em;
  color: $primary;
}

* + .post-preview { margin-top: 30px; }

* + .post-preview__time { margin-top: 5px; }

@include media-breakpoint-up(md) {
  * + .post-preview { margin-top: 35px; }
}

@include media-breakpoint-between(lg, lg) {
  .post-preview {
    font-size: 13px;
    @include groupX(15px);
  }

  .post-preview__image-outer {
    max-width: 60px;
  }
}

//
// Post Creative
//
.post-creative {
  background: $white;
  box-shadow: $shadow-area-1;

  img {
    width: 100%;
  }

  * + .row { margin-top: 35px; }
}

.post-creative__header,
.post-creative__main,
.post-creative__footer {
  padding-right: 22px;
  padding-left: 25px;
}

.post-creative__header {
  padding-top: 25px;
  padding-bottom: 15px;
}

.post-creative__footer {
  padding-top: 25px;
  padding-bottom: 30px;
}

.post-creative__title {
  font-family: $font-family-sec;
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0;
  color: $headings-color;

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }
}

.post-creative__time {
  letter-spacing: .05em;
}

.post-creative__carousel {
  .owl-prev,
  .owl-next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    color: $white;
    background: rgba($black, .38);
    transition: .25s cubic-bezier(0, 0, 0.2, 1);

    &:hover {
      color: $white;
      background: $primary;
    }
  }
}

.post-creative__meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  @include group(20px, 15px);

  > * {
    margin-top: 0;
  }
}

.post-creative__image-link {
  position: relative;
  overflow: hidden;
  display: block;
  //backface-visibility: hidden;
  background: $gray-800;

  img {
    opacity: 1;
    transform: scale3d(1, 1, 1);
    transition: .4s cubic-bezier(0, 0, 0.2, 1);
  }

  &:hover {
    img {
      opacity: .9;
      transform: scale3d(1.03, 1.03, 1.03);
    }
  }
}

* + .post-creative { margin-top: 35px; }
html * + .post-creative__carousel { margin-top: 0; }
* + .post-creative__media { margin-top: 15px; }
.post-creative__media + .post-creative__main { margin-top: 25px; }

@include media-breakpoint-up(sm) {
  .post-creative__title {
    font-size: 22px;
  }
}

@include media-breakpoint-up(md) {
  .post-creative__title {
    font-size: 26px;
  }

  .post-creative__media + .post-creative__main { margin-top: 45px; }
}

@include media-breakpoint-between(lg, lg) {
  .post-creative__title {
    font-size: 22px;
  }

  .post-creative__media + .post-creative__main { margin-top: 24px; }
}

@include media-breakpoint-up(xl) {
  .post-creative__main {
    * + p { margin-top: 25px; }
    .quote-line + * { margin-top: 35px; }
  }

  .post-creative__header,
  .post-creative__main,
  .post-creative__footer {
    padding-right: 52px;
    padding-left: 52px;
  }

  .post-creative__header {
    padding-top: 45px;
    padding-bottom: 20px;
  }

  .post-creative__footer {
    padding-top: 35px;
    padding-bottom: 40px;
  }

  .post-creative__title {
    font-size: 30px;
    line-height: 1.2;
  }
}

//
// Post Info
//
.post-info {
  text-align: left;
}

.post-info__figure {
  img {
    min-width: 100%;
    width: auto;
  }
}

.post-info__main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 20px;
  background: $gray-100;
}

.post-info__divider {
  position: relative;
  font-size: 0;
  line-height: 0;

  &::before {
    content: '';
    display: inline-block;
    width: 60px;
    height: 2px;
    background: $primary;
  }
}

* + .post-info { margin-top: 40px; }

* + .post-info__divider { margin-top: 20px; }

.post-info__divider + * { margin-top: 20px; }

@include media-breakpoint-down(xs) {
  .post-info {
    max-width: 370px;
    margin-left: auto;
    margin-right: auto;
  }
}

@include media-breakpoint-up(md) {
  .post-info {
    display: flex;
    @include groupX(33px);
  }

  .post-info__figure {
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
    flex-shrink: 0;
    max-width: (100 / 3) * 1%;

    img {
      min-width: 100%;
      max-width: none;
      flex-shrink: 0;
    }
  }

  .post-info__main {
    width: 100%;
    padding: 30px 30px 40px 30px;
    flex-grow: 1;
    
    > * {
      max-width: 100%;
    }
  }

  .post-info__divider {
    &::before {
      width: 85px;
      height: 3px;
    }
  }
}

@include media-breakpoint-up(xl) {
  .post-info__main {
    padding: 40px 50px 40px 80px;
  }

  * + .post-info__divider { margin-top: 30px; }
  .post-info__divider + * { margin-top: 30px; }
} 
