/**
*
* RD Navbar Classic
*/
.rd-navbar-classic {
  
  // RD Sidebar
  .rd-sidebar { }
  
  .rd-sidebar__button {
    &.active {
      background: darken($gray-700, 5%);

      .rd-sidebar__shape {
        transform: scale(.8, .8);
        fill: rgba($white, .09);
      }
    }
  }

  .rd-sidebar__button,
  .rd-sidebar__shape {
    transition: .15s;
  }

  .rd-sidebar__list {
    transition: .33s;
    opacity: 0.9;
    box-shadow: 0 0 12px 1px rgba($gray-800, .3);
    transform: translate3d(101%, 0, 0);

    &.active {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
  
  // Static
  &.rd-navbar-static {  
    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      border-bottom: $rd-navbar-border;
    }
    
    .rd-navbar-panel {
      flex-grow: 1;
    }
    
    .rd-navbar-main {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 220px 0 35px;
      margin: 0 auto;
      
      > * + * {
        margin-left: 60px;
      }
    }
  
    // RD Navbar Brand
    .rd-navbar-brand {
      display: inline-block;
      font-size: 0;
      line-height: 0;
      
      img {
        width: auto;
        height: auto;
        max-width: 200px;
        max-height: 200px; 
      }
    }
  
    .rd-navbar-main-element {
      display: flex;
      align-items: center;
    }
  
    // RD Navbar Nav
    .rd-navbar-nav-outer {
      position: relative;
      flex-shrink: 0;
    }
    
    .rd-navbar-nav { }
    
    .rd-nav-link {
      position: relative;
      display: inline-block;
      padding: 25px 0;
      
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 4px;
        background: $primary;
        width: 0;
        pointer-events: none;
        transition: .22s;
      }
    }
    
    .rd-nav-item {
      &:hover,
      &.focus,
      &.active {
        .rd-nav-link::after {
          width: 100%;
        }
      }
    }
    
    // RD Navbar Menu 
    .rd-menu {
      margin-top: -1px;
    }
    
    .rd-navbar-megamenu {
      margin-left: -30px;
      right: -30px;
      left: auto;
      width: auto;
      min-width: 660px;
    }
    
    // RD Sidebar
    .rd-sidebar { }

    .rd-sidebar__button {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0; 
      width: 200px;
      z-index: 2;
    }
    
    .rd-sidebar__list {
      top: 0;
      bottom: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 200px;
      padding: 20px 20px 5vh;
    }

    .rd-sidebar__list-item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      
      &:first-child {
        .rd-sidebar__list-item-icon {
          padding-top: 0;
        }
      }
      
      &:nth-child(n + 2) {
        flex-grow: 1;

        &::before {
          content: '';
          display: block;
          flex-grow: 1;
          min-height: 30px;
          width: 1px;
          background: rgba($white, .2);
          pointer-events: none;
        }
      }
    }

    .rd-sidebar__list-item-inner {
      position: relative;
      &:hover {
        .rd-sidebar__list-item-main {
          opacity: 1;
          visibility: visible;
          transform: scale3d(1, 1, 1);
        }
      }
    } 

    .rd-sidebar__list-item-icon {
      position: relative;
      padding: 20px 5px;
      font-size: 42px;
      cursor: pointer;
      text-align: center;
      
      &.linearicons-share2 { font-size: 38px; }
      &.linearicons-bubble-dots { font-size: 40px; }
    }

    .rd-sidebar__list-item-main {
      position: absolute;
      right: 100%;
      bottom: 5px;
      display: flex;
      align-items: center;
      min-width: 200px;
      height: 65px;
      padding: 10px 20px 10px 30px;
      background: $gray-700;
      border-left: 7px solid $primary;
      transition: .2s cubic-bezier(0.4, 0.66, 0.63, 1.04);
      transform: scale3d(.8, 1, 1);
      opacity: 0;
      visibility: hidden;
      transform-origin: 100% 50%;
      
      .list-inline {
        white-space: nowrap;
      }
      
      &:hover {
        opacity: 1;
        visibility: visible;
        transform: scale3d(1, 1, 1);
      }
    }

    .rd-sidebar__list-item-link {
      font-size: 17px;
    }
    
    // Extra large ≥1200px
    @include media-breakpoint-up(xl) {
      .rd-nav-link {
        padding: 36px 0 38px;
        
        &::after {
          height: 7px;
        }
      }
    } 
    
    @include media-breakpoint-up(xxl) {
      .rd-navbar-main {
        padding: 0 220px 0 75px;
      }   
    } 
    
    // RD Navbar Stuck || Clone
    &.rd-navbar--is-stuck,
    &.rd-navbar--is-clone {
      box-shadow: 0 0 12px 0 rgba($gray-800, .05);
      
      .rd-navbar-aside-outer {
        display: none;
      }
      
      // RD Navbar Nav
      .rd-nav-link {
        padding: 25px 0 29px;
        
        &::after {
          height: 6px;
        }
      }
    
      .rd-navbar-nav-item {
        > .rd-navbar-submenu {
          margin-top: 17px;
        }
      }
    }
  }
  
  // Fixed 
  &.rd-navbar-fixed {
    // BTC to USD plugin
    .btc-to-usd {
      position: fixed;
      right: 0;
      top: 4px;
      z-index: $zindex-rd-navbar + 1;
    }
    // RD Sidebar
    .rd-sidebar { }
    
    .rd-sidebar__button {
      position: fixed;
      top: 0;
      right: 0;
      width: 60px;
      height: $rd-navbar-fixed-height;
      background: darken($gray-700, 2%);
    }

    @media (max-width: 359px) {
      /*.btc-to-usd {
        right: 42px;  
      }*/
      
      .rd-sidebar__button {
        width: 50px;
      }
    }

    .rd-sidebar__shape {
      width: 21px;
      height: 20px;
    }

    .rd-sidebar__list {
      top: $rd-navbar-fixed-height;
      min-width: 240px;
      padding: 20px;
      z-index: -1;
    }

    .rd-sidebar__list-item + .rd-sidebar__list-item {
      margin-top: 10px;
    }

    .rd-sidebar__list-item-inner {
      display: flex;
      align-items: center;

      > * + * {
        margin-left: 15px;
      }

      a {
        display: block;
        padding: 10px 0 7px;
      }
    }

    .rd-sidebar__list-item-main {
      flex-grow: 1;
    }

    .rd-sidebar__list-item-icon {
      font-size: 20px;
      color: $primary;
    }
  }
}
