// Pagination 
// Tooltips
// Modal


//
// Color system
//

// Gray colors
$white:  #fff;
$gray-100: #f6f6f6;
$gray-200: #ebebeb;
$gray-300: #d7d7d7;
$gray-400: #b7b7b7;
$gray-500: #9b9b9b;
$gray-600: #868e96;
$gray-700: #373b46; 
$gray-800: #151515;
$gray-900: #111;
$black:  #000;


$color-1: #303443;
$color-2: #676a72;
$color-3: #30333b;
$color-4: #ccc;
$color-5: #919191;
$color-6: #1d2c3f;


// 
// Body 
//  
$body-bg:       $white;
$body-color:    $gray-800;


$primary: #fab915; 
$primary-darken: darken(desaturate(adjust_hue($primary, 0.044266315726495975), 7.151931970253941%), 4.705882352941181%);;
$theme-colors: (); 

// Accent colors
$danger-color:    #e86557;
$success-color:   #58ad96;

 
// Additional colors
$facebook:                #4d70a8;
$facebook-light:          #748abc;
$facebook-darken:         darken($facebook, 5%);

$twitter:                 #02bcf3;
$twitter-light:           #81cbdb;
$twitter-darken:          darken($twitter, 5%);
 
$google:                  #e2411e;
$google-light:            #de7272;
$google-darken:           darken($google, 5%);


//
// Page
//
$page-boxed-width:               1600px;
$page-boxed-background-color:    $gray-100;
$page-boxed-background-image:    url(../images/bg-pattern-boxed.png);


//
// Components
//
// Define common padding and border radius sizes and more.
$border-width: 1px;

$border-radius:          0;
$border-radius-lg:       .3rem;
$border-radius-sm:       .2rem;

$transition-base:        all .3s ease-in-out;

// Map
//
$map-height: 200px;
$map-xs-height: 250px;
$map-md-height: 450px;


// Custom
$shadow-area-1:   0 0 16px 0 rgba($black, 0.09);
$shadow-area-2:   1px 1px 10px 0 rgba(0, 0, 0, 0.2);


//
// Fonts
//
// Font, line-height, and color for body text, headings, and more.
$font-family-sans-serif:   "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
$font-family-sans-serif-1: "Exo 2", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
$font-family-sans-serif-2: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
$font-family-serif:        "Roboto Slab", "Times New Roman", Times, serif;
$font-family-monospace:    Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;

$font-family-base:       $font-family-sans-serif;
$font-family-sec:        $font-family-sans-serif-1;
$font-family-trd:        $font-family-sans-serif-2;

$font-weight-bold: 700;

// Base fonts
$font-size-base: 14px;
$font-size-lg:   16px;
$font-size-sm:   12px;

$_lh-base: 22px;
$_lh-lg:   22px;
$_lh-sm:   18px;

$font-weight-base: 400;

// ** < Calculated font values >
$line-height-base: ($_lh-base / $font-size-base);
$line-height-lg:   ($_lh-lg / $font-size-lg);
$line-height-sm:   ($_lh-sm / $font-size-sm);
// ** </ Calculated font values >


// Headings
$headings-font-family:   $font-family-sec;
$headings-font-weight:   700;
$headings-line-height:   1.1;
$headings-color:         $gray-800;

// Template Heading Values
$_h1-fsz: 60; 
$_h2-fsz: 45;
$_h3-fsz: 35;
$_h4-fsz: 24;
$_h5-fsz: 20;
$_h6-fsz: 14;

$_h1-lh: round($_h1-fsz * 1.2);
$_h2-lh: 48;
$_h3-lh: round($_h3-fsz * 1.2);
$_h4-lh: round($_h4-fsz * 1.2);
$_h5-lh: round($_h5-fsz * 1.2);
$_h6-lh: round($_h6-fsz * 1.6);

// ** < Calculated Headings values >
$h1-font-size: $_h1-fsz * 1px;
$h2-font-size: $_h2-fsz * 1px;
$h3-font-size: $_h3-fsz * 1px;
$h4-font-size: $_h4-fsz * 1px;
$h5-font-size: $_h5-fsz * 1px;
$h6-font-size: $_h6-fsz * 1px;

$h1-line-height: ($_h1-lh / $_h1-fsz); 
$h2-line-height: ($_h2-lh / $_h2-fsz);
$h3-line-height: ($_h3-lh / $_h3-fsz);
$h4-line-height: ($_h4-lh / $_h4-fsz);
$h5-line-height: ($_h5-lh / $_h5-fsz);
$h6-line-height: ($_h6-lh / $_h6-fsz);
// ** </ Calculated Headings values >


// Icon Fonts
$fa:  'FontAwesome';
$mdi: 'Material Design Icons';
$mi:  'Material Icons';

// Lead
$lead-font-size:   20px;
$lead-line-height: 24px;
$lead-font-weight: 300;

// Hr
$hr-border-color: $gray-200;
$hr-border-width: $border-width;

// Mark
$mark-padding: 5px 10px;
$mark-bg: $primary;

$dt-font-weight: inherit;
$list-inline-padding: 5px;

// Code
$code-font-size:              90%;
$code-padding-y:              10px;
$code-padding-x:              5px;
$code-color:                  $gray-900;
$code-bg:                     $gray-100;


//
// Links
//
$link-color:            $primary;
$link-decoration:       none;
$link-hover-color:      darken($link-color, 15%); 
$link-press-color:      darken($link-color, 15%);
$link-hover-decoration: underline;


// Z-index master list
//
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.

$zindex-dropdown:           1000;
$zindex-sticky:             1020;
$zindex-fixed:              1030; 
$zindex-modal-backdrop:     1110;
$zindex-modal:              1120;
$zindex-popover:            1060;
$zindex-tooltip:            1020;
// Custom
$zindex-rd-navbar:          1030;
$zindex-sidebar:            1031;  
$zindex-to-top:             100; 


//
// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.
$grid-breakpoints: (
  xs:   0,
  sm:   576px,
  md:   768px,
  lg:   992px,
  xl:   1200px,
  xxl:  1600px
);
 
@include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include _assert-starts-at-zero($grid-breakpoints);


$screen-fullhd: 1600px;


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
    sm:   540px,
    md:   720px,
    lg:   960px,
    xl:   1200px
);
@include _assert-ascending($container-max-widths, "$container-max-widths");


//
// Grid columns
//
// Set the number of columns and specify the width of the gutters.
$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-gutter-width-narrow: 20px;


//
// Buttons
//

$input-btn-padding-y:       22px;
$input-btn-padding-x:       37px;
$input-btn-font-size:       14px;
$input-btn-line-height:     1.25;

$input-btn-padding-y-sm:    15px;
$input-btn-padding-x-sm:    25px;
$input-btn-line-height-sm:  1.5;

$input-btn-padding-y-lg:    26px;
$input-btn-padding-x-lg:    55px;
$input-btn-line-height-lg:  1.5;


$btn-font-family:                $font-family-trd;
$btn-font-weight:                700; 
$btn-letter-spacing:             0;
$btn-text-transform:             uppercase;

$btn-box-shadow:                 0;
$btn-focus-box-shadow:           0;
$btn-active-box-shadow:          0;

$btn-link-disabled-color:        $gray-600;

$btn-block-spacing-y:            .5rem;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:              $border-radius;
$btn-border-radius-lg:           $border-radius-lg;
$btn-border-radius-sm:           $border-radius-sm; 

$btn-transition:                 all .15s ease-in-out;

//
// Forms
//
$form-input-height:             60px;
$form-input-lg-height:          66px;
$form-input-sm-height:          40px;

$form-input-color:              $gray-800;
$form-input-color-placeholder:  $form-input-color;

$form-input-background:         $white;
$form-input-border-color:       $gray-200;
$form-input-border-radius:      $border-radius;

$form-input-font-size:          14px;
$form-input-line-height:        22px;
$form-input-font-weight:        400;

$form-input-border:             1px solid $form-input-border-color;
$form-input-padding-horizontal: 19px;
$form-input-line-height:        round($form-input-font-size * 1.7);

$form-input-padding-vertical:   round(($form-input-height - $form-input-line-height) / 2) + 1px;
$form-input-padding:            $form-input-padding-vertical $form-input-padding-horizontal;

@if ($form-input-border != none) {
  $form-input-padding-vertical: round(($form-input-height - $form-input-line-height - (nth($form-input-border, 1) * 2)) / 2);
  $form-input-padding: $form-input-padding-vertical $form-input-padding-horizontal;
}

$form-textarea-default-height:       235px;
$form-textarea-default-min-height:   $form-input-height;
$form-textarea-default-max-height:   round($form-textarea-default-height * 1.7);


$form-feedback-focus-color:   $primary;
$form-feedback-valid-color:   $success-color;
$form-feedback-invalid-color: $danger-color;


//
// Pagination
//
$pagination-padding-y:                 10px;
$pagination-padding-x:                 10px;
$pagination-font-size:                 14px;
$pagination-line-height:               24px;

$pagination-color:                     $gray-500;
$pagination-bg:                        $white;
$pagination-border-width:              2px;
$pagination-border-color:              $gray-300;

$pagination-hover-color:               $white;
$pagination-hover-bg:                  $primary;
$pagination-hover-border-color:        $primary;

$pagination-active-color:              $white;
$pagination-active-bg:                 $primary;
$pagination-active-border-color:       $primary;

$pagination-disabled-color:            $gray-400;
$pagination-disabled-bg:               $gray-300;
$pagination-disabled-border-color:     $gray-300;

//
// Tooltips
//
$tooltip-max-width:           200px;
$tooltip-color:               $white;
$tooltip-bg:                  $gray-700;
$tooltip-opacity:             1;
$tooltip-padding-y:           6px;
$tooltip-padding-x:           10px;
$tooltip-margin:              0;

$tooltip-arrow-width:         6px;
$tooltip-arrow-height:        6px;
$tooltip-arrow-color:         $tooltip-bg;




// Modals

// Padding applied to the modal body
$modal-inner-padding:               15px;

$modal-dialog-margin:               10px;
$modal-dialog-margin-y-sm-up:       30px;

$modal-title-line-height:           $line-height-base;

$modal-content-bg:                  $white;
$modal-content-border-color:        rgba($black,.2);
$modal-content-border-width:        $border-width;
$modal-content-box-shadow-xs:       0 3px 9px rgba($black,.5);
$modal-content-box-shadow-sm-up:    0 5px 15px rgba($black,.5);

$modal-backdrop-bg:                 $black;
$modal-backdrop-opacity:            .5;
$modal-header-border-color:         $gray-200;
$modal-footer-border-color:         $modal-header-border-color;
$modal-header-border-width:         $modal-content-border-width;
$modal-footer-border-width:         $modal-header-border-width;
$modal-header-padding:              15px;

$modal-lg:                          800px;
$modal-md:                          600px;
$modal-sm:                          300px;

$modal-transition:                  transform .3s ease-out;
