/*
*
* Footers
*/

//
// Footer Classic
//

footer.footer-classic {
  color: rgba($white, .5);
  background: $gray-700;
  
  .lead,
  .large {
    color: $white;
  }
}

.footer-classic__main {
  padding: 60px 0 40px;
}

.footer-classic__aside {
  padding: 20px 0;
  @include group(20px, 22px);
  text-align: center;
}

// Medium ≥576px
@include media-breakpoint-up(sm) {
  .footer-classic__aside {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
  } 
}

// Medium ≥768px
@include media-breakpoint-up(md) {
  .footer-classic__main {
    padding: 70px 0 40px;
  }
  
  .footer-classic__aside {
    padding: 35px 0;
  }
}

// Large ≥992px
@include media-breakpoint-between(lg, lg) {
  .form {
    .button {
      padding-left: 20px;
      padding-right: 20px;
    }
  } 
}
