/*
*
* Breadcrumbs
*/
$breadcrumb-padding-vertical: 8px;
$breadcrumb-padding-horizontal: 15px;
$breadcrumb-bg: $gray-800;
$breadcrumb-color: $white;
$breadcrumb-active-color: $white;
$breadcrumb-separator: "\f105";

.breadcrumbs-custom {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: (410 / 1920) * 100vw;
  text-align: center;
  background-color: $breadcrumb-bg;

  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: inherit;
    align-items: center;

    &::before {
      content: '';
      display: block;
      flex-grow: 4;
    }
  }
}

.breadcrumbs-custom__main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 0 40px;
  flex-grow: 8;
}

.breadcrumbs-custom__path {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px 10px 20px;
  background: $gray-700;
  @include spacing(26px, 10px);

  a {
    display: inline;
    vertical-align: middle;
    &, 
    &:active,
    &:focus {
      color: $breadcrumb-color;
    }

    &:hover {
      color: $primary;
    }
  }

  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: .05em;
    text-transform: uppercase;

    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate3d(0, -50%, 0);
      height: 21px;
      width: 1px;
      background: rgba($white, .3);
    }
  }

  li:last-child {
    &:after { display: none; }
  }

  //a:hover,
  //li.active {
  //
  //}

  li.active {
    top: 1px;
  }
}

.breadcrumbs-custom__text {
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
}
 
* + .breadcrumbs-custom__text { margin-top: 15px; }

@include media-breakpoint-up(sm) {
  .breadcrumbs-custom {
    @include spacingX(60px); 
  }
  
  .breadcrumbs-custom__path {
    padding: 10px 20px 20px;
    @include spacingX(36px);
  }
}

@include media-breakpoint-up(md) {
  .breadcrumbs-custom__main {
    padding-top: 50px;
    padding-bottom: 45px;
  }
  
  * + .breadcrumbs-custom__text { margin-top: 22px; }
}

@include media-breakpoint-up(xl) {
  .breadcrumbs-custom__main {
    padding-bottom: 75px;
  }

  * + .breadcrumbs-custom__text { margin-top: 30px; }
}

@include media-breakpoint-up(xxl) {
  .breadcrumbs-custom {
    min-height: 410px;
  }
}
