/*
*
* Grid modules
*/

//
// Grid demonstration
//
.grid-demonstration {
  $grid-heading: unquote("h5, .heading-5"); 
  padding: 12px 8px;
  letter-spacing: 0;
  text-align: left;

  @include media-breakpoint-down(lg) {
    #{$grid-heading} {
      font-size: 14px;
    }
  }

  @include media-breakpoint-down(xl) {
    p {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.container-fluid.grid-demonstration {
  @include media-breakpoint-down(md) {
    .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
  
  [class^='col'] {
    padding: 5px;
    @include media-breakpoint-down(md) {
      p {
        font-size: 14px;
      }

      * + p { margin-top: 10px; }
    }

    @include media-breakpoint-up(md) {
      padding: 15px 10px;
    }

    @include media-breakpoint-up(lg) {
      padding: 25px 15px;
    }

    @include media-breakpoint-up(xl) {
      padding: 30px 15px 30px 30px;
    }

    @include media-breakpoint-up(xxl) {
      padding: 50px 100px;

      * + p {
        margin-top: 24px;
      }
    }
  }

  .row {
    @include media-breakpoint-up(xxl) {
      margin-left: -70px;
      margin-right: -70px;
    }
  }
}

//
// Grid system bordered
//
.grid-system-bordered {
  [class*='col'] {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .row {
    border-bottom: 1px solid $gray-200;
  }

  @include media-breakpoint-up(md) {
    [class*='col'] {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  } 

  @include media-breakpoint-up(xl) {
    [class*='col'] {
      padding-top: 45px;
      padding-bottom: 45px;
    }
  }
} 

//
// Grid system outline
//
.grid-system-outline {
  .row {
    border: 1px solid $gray-200;

    &:not(:last-child) { border-bottom-width: 0; }
  }

  [class*='col']:not(:last-child) {
    border-right: 1px solid $gray-200;
    margin-right: -1px;
  }

  * + .row { margin-top: 45px; }
  .row + .row { margin-top: 0; }
}


// Custom Grid
//
// Change $enable-grid-classes-custom: true
// that would generate custom grid (example: 10 columns)

$enable-grid-classes-custom: false;

@mixin make-grid-columns-custom($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {
  @each $breakpoint in map-keys($breakpoints) {
    $infix: breakpoint-infix($breakpoint, $breakpoints);

    @include media-breakpoint-up($breakpoint, $breakpoints) {
      @for $i from 1 through $columns {
        .col#{$infix}-#{$i} {
          @include make-col($i, $columns);
        }
      }
    }
  }
}

@if $enable-grid-classes-custom {
  .row-ten {
    @include make-grid-columns-custom(10);
  }

  .row-xl-ten {
    @include make-grid-columns-custom(10, 30px, (xl: map-get($grid-breakpoints, xl), xlg: map-get($grid-breakpoints, xlg), xxl: map-get($grid-breakpoints, xxl)));
  }
}

.row-narrow {
  margin-left: - round($grid-gutter-width-narrow / 2);
  margin-right: - round($grid-gutter-width-narrow / 2);
  
  > [class*='col'] {
    padding-left: round($grid-gutter-width-narrow / 2);
    padding-right: round($grid-gutter-width-narrow / 2);
  }
}

//
// Range Grid System
//
.range {
  display: flex;
  flex-wrap: wrap;

  img {
    display: block;
  }
}

.cell-inner {
  width: 100%;
}

[class*='cell'] {
  position: relative;
  width: 100%;
  min-height: 1px;

  .cell-inner {
    padding-right: 15px;
    padding-left: 15px;
  }

  &:nth-child(odd) {
    .cell-inner {
      margin-left: auto;
    }
  }
}

@include media-breakpoint-up(lg) {
  .cell-lg-6 {
    flex: 0 0 50%;
    max-width: 50%;

    .cell-inner {
      max-width: map-get($container-max-widths, lg) / 2;
    }
  }
}

@include media-breakpoint-up(xl) {
  .cell-lg-6 {
    flex: 0 0 50%;
    max-width: 50%;

    .cell-inner {
      max-width: map-get($container-max-widths, xl) / 2;
    }
  }
}

// Custom
.row-bordered-1 {
  > [class*='col'] {
    position: relative;
    padding: 35px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 0 solid $gray-200;

    &::before {
      border: inherit;
      position: absolute;
      top: 0;
      left: 50%;
      width: 200vw;
      transform: translate3d(-50%, 0, 0);
      pointer-events: none;
    }
  }

  > [class*='col'] + [class*='col'] {
    border-top-width: 1px;
  }
}

@include media-breakpoint-up(sm) {
  .row-bordered-1 {
    > [class*='col'] + [class*='col'] {
      border-top-width: 0;
    }
    
    .col-sm-6 {
      padding: 50px 15px;
      border-right-width: 1px;

      &:nth-child(2n) {
        border-right-width: 0;
      }

      &:nth-child(3n) {
        &::before {
          content: '';
          border-top-width: 1px;
        }
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  .row-bordered-1 {
    .col-sm-6 {
      &:nth-child(3n) {
        &::before {
          display: none;
        }
      }
    }

    .col-lg-4 {
      padding: 60px 20px;
      &:nth-child(n) {
        border-right-width: 1px;
      }

      &:nth-child(3n) {
        border-right: 0;
      }
      
      &:nth-child(4n) {
        &::before {
          content: '';
          display: block;
          border-top-width: 1px;
        }
      }
    }
  } 
}

// Extra large ≥1200px
@include media-breakpoint-up(xl) {
  .row-bordered-1 {
    .col-lg-4 {
      padding: 75px 20px;
    }
  }
}

.container-md-smaller {
  @include media-breakpoint-between(md, md) {
    max-width: 100%;
  }
}

//
// Row Bordered
//

@include media-breakpoint-between(lg, lg) {
  .row-2-columns-bordered > [class*='col']:nth-child(1) {
    padding-right: 50px;
  }
}

@include media-breakpoint-up(lg) {
  .row-2-columns-bordered > [class*='col']:nth-child(1) {
    position: relative;
  }

  .row-2-columns-bordered > [class*='col']:nth-child(1)::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 16px;
    border-right: 1px solid $gray-100;
  }
}

@media (min-width: 1200px) {
  .row-2-columns-bordered > [class*='col']:nth-child(1)::after {
    right: -10.33333%;
  }
}
