/*
*
* Typography
*/

//
// Body
//
body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
  font-weight: $font-weight-base;
  color: $body-color;
  background-color: $body-bg;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: subpixel-antialiased;
}

//
// Headings
//
#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: $headings-font-weight;
  color: $headings-color;

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }
}

h1,
.heading-1 {
  font-size: 32px;
  line-height: 1.3;

  @include media-breakpoint-up(md) {
    font-size: 42px;
  }

  @include media-breakpoint-up(lg) {
    font-size: 48px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 54px;
  }

  @include media-breakpoint-up(xxl) {
    font-size: $h1-font-size;
    line-height: $h1-line-height;
  }
}

h2,
.heading-2 {
  font-size: 24px;
  line-height: 1.3;
  font-weight: 300;

  @include media-breakpoint-up(md) {
    font-size: 38px;
    line-height: 1.2;
  }

  @include media-breakpoint-up(xl) {
    font-size: $h2-font-size;
    line-height: $h2-line-height;
  }
}

h3,
.heading-3 {
  font-size: 22px;
  line-height: 1.5;
  font-weight: 300;

  @include media-breakpoint-up(xl) {
    font-size: 28px;
  }

  @include media-breakpoint-up(xxl) {
    font-size: $h3-font-size;
    line-height: $h3-line-height;
  }
}

h4,
.heading-4 {
  font-size: 18px;
  line-height: 1.3;
  font-weight: 400;
  letter-spacing: .025em;

  @include media-breakpoint-up(md) {
    font-size: 20px;
  }

  @include media-breakpoint-up(xl) {
    font-size: $h4-font-size;
    line-height: $h4-line-height;
  }
}

h5,
.heading-5 {
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;

  @include media-breakpoint-up(md) {
    font-size: 18px;
  }

  @include media-breakpoint-up(xl) {
    font-size: $h5-font-size;
    line-height: $h5-line-height;
  }
}

h6,
.heading-6 {
  font-family: $font-family-base;
  font-size: 12px;
  font-weight: 700;
  line-height: $h6-line-height;
  text-transform: uppercase;
  letter-spacing: .35em;
  color: rgba($gray-700, .6);

  // Medium ≥768px
  @include media-breakpoint-up(md) {
    font-size: $h6-font-size;
    letter-spacing: .5em;
  }
}

p {
  letter-spacing: .05em;

  span[style*='max=width'] {
    display: inline-block;
  }
}

//
// Emphasis
//
small,
.small {
  display: block;
  font-size: $font-size-sm;
  line-height: $line-height-sm;
}

mark,
.mark {
  padding: 3px 5px;
  color: $white;
  background: $primary;
}

// Big text
.big {
  font-size: $font-size-lg;
  line-height: $line-height-lg;
  letter-spacing: .05em;
}

.lead {
  font: italic $lead-font-weight #{$lead-font-size}/#{$lead-line-height} $font-family-sec;
  letter-spacing: .025em;
}

.large {
  font: italic $lead-font-weight 17px/24px $font-family-sec;
  letter-spacing: .025em;
}

.text-extra-large {
  font: 700 85px/70px $font-family-sec;
  letter-spacing: -.05em;
  color: rgba($gray-700, .08);

  @include media-breakpoint-up(md) {
    font-size: 120px;
    line-height: .8;
  }

  @include media-breakpoint-up(lg) {
    font-size: 145px;
  }

  @include media-breakpoint-up(xl) {
    font-size: 285px;
  }
}

.text-xl {
  font: 700 85px/70px $font-family-sec;
  letter-spacing: -.05em;
  color: rgba($gray-700, .08);

  @include media-breakpoint-up(md) {
    font-size: 200px;
    line-height: .9;
  }

  @include media-breakpoint-up(xl) {
    font-size: 285px;
  }
}

%context-dark {
  .text-xl {
    color: rgba($white, 1);
  }
}

code {
  padding: $code-padding-y $code-padding-x;
  border-radius: $border-radius-sm;
  font-size: $code-font-size;
  color: $code-color;
  background: $code-bg;
}

strong {
  font-weight: 700;
}

.text-initial-letter {
  font-family: $font-family-sec;
  font-size: 17px;
  line-height: 1.45;
  font-weight: 300;

  &:first-letter {
    padding-right: 10px;
    font-size: 2.8em;
    line-height: .8;
    float: left;
  }

  &::after {
    content: '';
    clear: both;
  }

  @include media-breakpoint-up(md) {
    &:first-letter {
      padding-right: 20px;
      font-size: 4.9em;
    }
  }

  @include media-breakpoint-up(xl) {
    font-size: 20px;
    line-height: 1.2;
    &:first-letter {
      font-size: 4.4em;
    }
  }
}

//
// Paragraph
//
p {
  [data-toggle='tooltip'] {
    padding-left: .25em;
    padding-right: .25em;
    color: $primary;
  }

  [style*='max-width'] {
    display: inline-block;
  }
}

//
// Other
// 
.decoration-heading-1 {
  position: relative;

  &::before {
    content: '';
    display: inline-block;
    margin-right: .55em;
    vertical-align: middle;
    width: .7em;
    border-bottom: 1px solid $gray-200;
  }
}

%context-dark {
  .decoration-heading-1 {
    &::before {
      border-color: rgba($white, .3);
    }
  }
}

// Medium ≥576px
@include media-breakpoint-up(sm) {
  .decoration-heading-1 {
    &::before {
      width: 1.7em;
      margin-right: .75em;
    }
  }
}

//
// Rights
//
.rights {
}

%context-dark {
  .rights {
    a {
      color: $white;

      &:hover {
        color: $primary;
      }
    }
  }
}
