/*
*
* Slick Carousel
*/

//
// Base styles
//
/*rtl:begin:ignore*/

$slick-font-path: "./fonts/" !default;
$slick-font-family: 'Material Design Icons' !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: "\f14a" !default;
$slick-next-character: "\f14f" !default;
$slick-dot-character: "•" !default;
$slick-dot-size: 6px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  user-select: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;

  &::before,
  &::after {
    content: "";
    display: table;
  }

  &::after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
    float: right;
  }
  img {
    display: block;
  }
  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}

.slick-arrow.slick-hidden {
  display: none;
}

@function slick-image-url($url) {
  @if function-exists(image-url) {
    @return image-url($url);
  } @else {
    @return url($slick-loader-path + $url);
  }
}

@function slick-font-url($url) {
  @if function-exists(font-url) {
    @return font-url($url);
  } @else {
    @return url($slick-font-path + $url);
  }
}

// Appearance styles
// --------------------------------------------------

.slick-list {
  .slick-loading & {
    background: $white slick-image-url("../images/ajax-loader.gif") center center no-repeat;
  }
}

/* Icons */
@if $slick-font-family == "slick" {
  @font-face {
    font-family: "slick";
    src: slick-font-url("slick.eot");
    src: slick-font-url("slick.eot?#iefix") format("embedded-opentype"), slick-font-url("slick.woff") format("woff"), slick-font-url("slick.ttf") format("truetype"), slick-font-url("slick.svg#slick") format("svg");
    font-weight: normal;
    font-style: normal;
  }
}

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  color: transparent;
  background-color: transparent;
  top: 50%;
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 9;
  
  &::before {
    display: block;
    transform: translate3d(0, 0, 0);
    font-family: $slick-font-family;
    font-size: 20px;
    line-height: 1;
    color: $slick-arrow-color;
    opacity: $slick-opacity-default;
  }
  
  &.slick-disabled:before {
    opacity: $slick-opacity-not-active;
  }
}

.slick-prev {
  left: 0;
  [dir="rtl"] & {
    left: auto;
    right: 0;
  }
  &:before {
    content: $slick-prev-character;
    [dir="rtl"] & {
      content: $slick-next-character;
    }
  }
}

.slick-next {
  right: 0;
  [dir="rtl"] & {
    left: 0;
    right: auto;
  }
  &:before {
    content: $slick-next-character;
    [dir="rtl"] & {
      content: $slick-prev-character;
    }
  }
}

/* Dots */

.slick-dots {
  position: absolute;
  bottom: 0;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
  font-size: 0;
  line-height: 0;
  color: transparent;
  
  li {
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
    cursor: pointer;
  }
  
  li button {
    position: relative;
    width: 17px;
    height: 17px;
    padding: 0;
    border: 3px solid $gray-300;
    border-radius: 50%;
   
    text-align: center;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    transition: .2s;

    &:hover,
    &:focus {
      border-color: $primary;
    }
  }

  li.slick-active button {
    border-color: $primary;
  }
}

* + .slick-slider { margin-top: 30px; }

/*rtl:end:ignore*/


//
// Slick Slider
//
.slick-group-slider {
  position: relative;
  overflow: hidden;
  background: $color-3;

  // Carousel Parent
  .carousel-parent-outer { 
    position: relative;
    z-index: 1;
    overflow: hidden;
    padding: 40px 15px 30px;
    background: rgba(darken($gray-700, 5%), .85);
  }

  .carousel-parent-shape {
    position: absolute;
    left: 70%;
    transform: translate3d(-70%, 0, 0);
    min-width: 100%;
    fill: #4497dd;
    opacity: .05;
  }

  .parallax-scene {
    display: none;
  }
  
  .carousel-parent {
    max-width: 500px;
    margin: 0 auto;

    h1,
    .heading-1 {
      text-transform: uppercase;
    }
    
    .slick-track {
      display: flex;
      align-items: flex-end;
    }

    .slick-prev,
    .slick-next {
      display: none !important;
    }

    .slick-dots {
      position: relative;
      margin-top: 30px;
      text-align: left;
    }
  }
  
  .carousel-child {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: auto;
    display: flex;
    width: 100%;
    margin: 0;

    .slick-list,
    .slick-track {
      display: flex;
    }
    
    .slick-list {
      width: 100%;
    }
    
    .slick-slide {
      width: 100%;
    }
    
    .item {
      height: auto;
      float: none;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
    }
  }

  .slick-slide-caption__footer {
    max-width: 200px;
    min-height: 50px;
  }
  
  * + .slick-slide-caption__footer { margin-top: 30px; }
}

.ie-10,
.ie-11 {
  .slick-group-slider {
    .carousel-child {
      .slick-slide {
        min-width: 100%;
      }
    }
  }
}


@include media-breakpoint-up(md) {
  .slick-group-slider {
    padding: 30px 0;

    // Carousel Parent
    .carousel-parent-outer {
      padding: 80px 30px 95px;
      background: rgba(darken($gray-700, 5%), .9);
    }

    .carousel-parent {
      max-width: 520px;
      .slick-prev,
      .slick-next {
        display: block !important;
        left: auto;
        right: auto;
        top: auto;
        transform: none;
        bottom: 20px;

        &::before {
          font-size: 27px;
          color: rgba($white, .3);
          transition: .22s;
        }

        &:hover {
          &::before {
            color: $primary;
          }
        }
      }

      .slick-prev {
        left: 230px;

        &::after {
          position: absolute;
          top: 55%;
          transform: translate3d(0, -50%, 0) rotate(30deg);
          right: -35px;
          content: '';
          display: block;
          height: 30px;
          width: 1px;
          background: rgba($white, .3);
        }
      }

      .slick-next {
        left: 315px;
      }

      .slick-dots {
        display: none !important;
      }
    }

    * + p { margin-top: 30px; }
    * + .slick-slide-caption__footer { margin-top: 40px; }
  } 
}

@include media-breakpoint-up(lg) {
  .slick-group-slider {
    // Carousel Parent
    .carousel-parent-outer {
      @include make-grid-parameter(max-width, 30px, map-get($container-max-widths, lg), 6);
      background: $gray-700;
    }

    // Carousel Child
    .carousel-child {
      max-width: calc(50% + 40px);
    }
  }
}

@include media-breakpoint-up(xl) {
  .slick-group-slider {

    // Carousel Parent
    .carousel-parent-outer {
      padding: 100px 40px 120px 0;
      @include make-grid-parameter(max-width, -30px, map-get($container-max-widths, xl), 7);
    }

    .carousel-parent {
      max-width: 490px;
      margin-right: 0;
      p {
        max-width: 490px;
      }
    }

    .slick-slide-caption__footer {
      max-width: 200px;
      min-height: 65px;
    }

    // Carousel Child
    .carousel-child {
      @include make-grid-parameter(max-width, 60px, map-get($container-max-widths, xl), 5);
    }

    // Parallax Js
    .parallax-scene {
      display: block;
      position: absolute;
      top: 0;
      right: -70px;
      bottom: 0;
      left: 0;

      > [class*='layer'] {
        position: absolute;
        left: auto;
      }
    }

    .layer {
      width: 100%;
    }

    .layer-1 {
      right: 610px;
      left: auto;
      width: 340px;
      bottom: -12px;
    }

    .layer-2 {
      top: 85px;
      right: 710px;
      width: 100px;
      transform: rotate(40deg);
      opacity: .22;
    }

    .layer-3 {
      top: -25px;
      right: 740px;
      width: 80px;
      opacity: .08;
    }

    .layer-4 {
      top: -45px;
      right: 600px;
      width: 80px;
      opacity: .15;
    }
  }
}

@include media-breakpoint-up(xxl) {
  .slick-group-slider {

    // Carousel Parent
    .carousel-parent {
      max-width: 560px;
    }

    // Parallax Js
    .parallax-scene {
      right: 0;
    }

    .layer-1 {
      right: 600px;
      width: 425px;
    }

    .layer-2 {
      right: 780px;
      width: 120px;
    }

    .layer-3 {
      right: 790px;
    }

    .layer-4 {
      top: -60px;
      right: 650px;
    }
  }
}

@media (min-width: 1880px) {
  .slick-group-slider {
    // Carousel Parent
    .carousel-parent-outer {
      padding: 125px 40px 195px;
      @include make-grid-parameter(max-width, -15px, map-get($container-max-widths, xl), 5);
    }

    .carousel-parent {
      max-width: 560px;
    }

    // Carousel Child
    .carousel-child {
      @include make-grid-parameter(max-width, 45px, map-get($container-max-widths, xl), 7);
    }

  }
}

//
// Slick slider profiles
//
.slick-slider_profiles {
  position: relative;
  padding-right: 50px;
  max-width: 480px;

  .slick-slider {
    position: static;
  }

  .profile-item {
    position: relative;
    display: flex;
    padding: 20px 15px;
    border: 0 solid $gray-200;
  }

  .profile-item + .profile-item {
    border-width: 1px 0 0 0;
  }

  .slick-prev,
  .slick-next {
    position: absolute;
    right: 0;
    left: auto;
    transform: none;
    padding: 16px 0;
    width: 32px;
    height: auto;
    transition: .22s;
    color: rgba($gray-800, .4);
    background-color: transparent;

    &::before {
      font-family: 'Material Design Icons';
      font-size: 28px;
      color: inherit;
    }

    &:hover {
      color: $primary;
    }
  }

  .slick-prev {
    top: auto;
    bottom: 50%;
    vertical-align: top;
    &::before {
      content: '\f156';
    }
  }

  .slick-next {
    top: 50%;
    vertical-align: bottom;
    border-top: 1px solid $gray-200;
    &::before {
      content: '\f144';
    }
  }
}

@include media-breakpoint-up(sm) {
  .slick-slider_profiles {
    padding-right: 75px;
    .item {
      display: flex;
    }
    
    .item .profile-item {
      justify-content: center;
      width: 50%;
      padding-top: 25px;
      padding-bottom: 25px;
      border-width: 0 1px 0 1px;
    }
    
    .profile-item + .profile-item {
      border-left-width: 0;
    }

    .slick-prev,
    .slick-next {
      right: 13px;
    }
  }
}

@include media-breakpoint-up(xl) {
  .col-lg-6:nth-child(2) {
    .slick-slider_profiles {
      margin-left: auto;
    }
  }
} 

//
// Testimonials slick
//
.slider-group_testimonials {
  position: relative;
  overflow: hidden;
  padding: 50px 0 80px;

  .carousel-parent {
    
    .slick-dots {
      margin-top: 20px;
      position: static;
      top: auto;
      right: 0;
      left: 0;
      bottom: 0;
      transform: none;
      width: auto;
    }
  }
  
  .carousel-child {
    display: none;
    pointer-events: none;
    user-select: none;
  }

  .text-extra-large {
    position: absolute;
    bottom: 0;  
    right: -5px;
    margin-left: auto;
    pointer-events: none;
    user-select: none;
    white-space: nowrap;
  }
  
  * + .carousel-parent { margin-top: 30px; }
}

* + .slider-group_testimonials { margin-top: 0; }

// Medium ≥576px
@include media-breakpoint-up(sm) {
  .slider-group_testimonials {
    .quote-classic__meta {
      padding-right: 40px;
    }

    .carousel-parent {
      .slick-dots {
        position: absolute;
        right: 0; 
        left: auto;
        bottom: 0;
        transform: none;
        margin: 0;
      }
    }
  } 
}

@include media-breakpoint-up(md) {
  .slider-group_testimonials {
    padding-top: 80px;
    padding-bottom: 150px;
    
    .carousel-child {
      position: absolute;
      left: -200px;
      bottom: 0;
      z-index: 2;
      width: 70%;
      display: block;
      margin: 0;
    }
  }
}

// Large ≥992px
@include media-breakpoint-up(lg) {
  .slider-group_testimonials {
    padding-top: 95px;

    .carousel-child {
      width: 65%;
    }
  }
}

@include media-breakpoint-up(xl) {
  .slider-group_testimonials {
    padding-top: 110px;
    padding-bottom: 260px;
    
    .carousel-child {
      max-width: 740px;
      left: auto;
      @include make-grid-parameter(right, 15px, map-get($container-max-widths, xl), 7);
    }
    
    .text-extra-large {
      right: auto;
      @include make-grid-parameter(left, -10px, map-get($container-max-widths, xl), 4);
      max-width: 54.5%;
    }
  }
} 
