/*
*
* Lists
*/

.list-wrap {
  border: .1px solid transparent;
}

// Vertical list
.list {
  > li + li {
    margin-top: 10px;
  }
}
 
.list-xs > li + li { margin-top: 3px; }
.list-sm > li + li { margin-top: 10px; }
.list-md > li + li { margin-top: 18px; }
.list-lg > li + li { margin-top: 25px; }
.list-xl > li + li { margin-top: 15px; }

@include media-breakpoint-up(md) {
  .list-xl > li + li { margin-top: 60px; }
}

//
// List inline
//
.list-inline {
  > li {
    display: inline-block;
    vertical-align: middle;
  }
}

html {
  .list-inline-xs {
    @include spacing(12px, 5px);
  }
  
  .list-inline-sm {
    @include spacing(24px, 8px);
  }
}

//
// List terms
//
.list-terms {
  dt + dd { margin-top: 10px; }
  dd + dt { margin-top: 25px; }

  @include media-breakpoint-up(md) {
    dt + dd { margin-top: 18px; }
    dd + dt { margin-top: 38px; }
  }
}

* + .list-terms { margin-top: 25px; }

//
// Index list
//
.index-list {
  counter-reset: li;

  > li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal-leading-zero);
        counter-increment: li;
      }
    }
  }
}

//
// Marked list
//
.list-marked {
  padding-left: 25px;
  text-align: left;

  > li {
    text-indent: -25px;
    padding-left: 25px;
    &::before {
      position: relative;
      display: inline-block;
      left: 25px;
      top: -1px;
      min-width: 25px;
      content: '\f105';
      font: 400 14px/24px 'FontAwesome';
      color: $gray-400;
    }
  } 

  > li + li { margin-top: 4px; } 
}

* + .list-marked { margin-top: 15px; }
p + .list-marked { margin-top: 10px; }

//
// Ordered List
//
.list-ordered {
  counter-reset: li;
  padding-left: 25px;
  text-align: left;

  > li {
    position: relative;
    padding-left: 25px;

    &:before {
      content: counter(li, decimal) '.';
      counter-increment: li;
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      width: 15px;
      color: $gray-400;
    }
  }

  > li + li { margin-top: 10px; }
}

* + .list-ordered { margin-top: 15px; }

// List Steps
.list-steps {
  text-align: center;
  counter-reset: list-steps-item;
}

.list-steps__item {
  p {
    letter-spacing: .05em;
  }

  * + P { margin-top: 20px; }
  
  &:hover {
    .list-steps__item-divider {
      box-shadow: 0 0 10px 1px rgba($primary, .2);
    }
  }
}

.list-steps__item-counter {
  &::before {
    content: counter(list-steps-item, decimal-leading-zero);
    counter-increment: list-steps-item;
    font: 100 40px $font-family-sec;
    line-height: 1;
    color: rgba($gray-800, .3);
    vertical-align: bottom;
  }
}

.list-steps__item-divider {
  position: relative;
  z-index: 1;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30px;
  height: 30px;
  background: $primary;
  border-radius: 50%;
  border: 7px solid lighten($primary, 25%);
  transition: .2s;
}

.list-steps__item-main {
  position: relative;
  padding-top: 20px;
  
  &::before {
    content: '';
    position: absolute;
    top: -15px;
    left: -35px;
    border-bottom: 1px solid $gray-200;
    width: calc(100% + 35px);
    pointer-events: none;
  }
}

.list-steps__item {
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  &:first-child {
    .list-steps__item-main::before {
      right: 0;
      left: auto;
      width: 100vw;
    }
  }
  
  &:last-child {
    .list-steps__item-main::before {
      width: 100vw;
    }
  }
}

* + .list-steps { margin-top: 30px; }
* + .list-steps__item-divider { margin-top: 18px; }
.list-steps__item + .list-steps__item { margin-top: 25px; }

// Medium ≥768px
@include media-breakpoint-up(md) {
  .list-steps {
    display: flex;
  }
  
  .list-steps__item {
    flex-basis: (100% / 3);
    flex-grow: 1;
    padding: 0 5px;
    
    &:first-child {
      .list-steps__item-counter::before {
        font-size: 30px;
      }
    }

    &:last-child {
      .list-steps__item-counter::before {
        font-size: 60px;
      }
    }
  }

  .list-steps__item-counter {
    line-height: 60px;
    &::before {
      font-size: 45px;
    }
  }

  .list-steps__item + .list-steps__item { margin-top: 0; }
}

@include media-breakpoint-between(md, md) {
  .list-steps {
    p {
      font-size: 12px;
    }
  }
}

@include media-breakpoint-up(lg) {
  * + .list-steps { margin-top: 45px; }
}

// Extra large ≥1200px
@include media-breakpoint-up(xl) {
  .list-steps__item {
    &:first-child {
      .list-steps__item-counter::before {
        font-size: 60px;
      }
    }
    
    &:last-child {
      .list-steps__item-counter::before {
        font-size: 100px;
      }
    }
  }
  
  .list-steps__item-counter {
    line-height: 100px;
    &::before {
      font-size: 81px;
    }
  } 
} 

// 
// List nav
// 
.list-nav {
  @include group(30px, 10px);
  font-family: $font-family-sec;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.2;
  letter-spacing: .025em;
  color: $body-color;
  
  li {
    display: inline-block;
  }
  
  button {
    @include reset-button;
  }

  a,
  button {
    color: inherit;
    transition: .22s;
    
    &:hover {
      color: $primary; 
    }
  }
}

// Medium ≥768px
@include media-breakpoint-up(xl) {
  .list-nav {
    font-size: 17px;
    @include groupX(46px);
  } 
}

%context-dark {
  .list-nav {
    color: $white;
  }
}

//
// List Bordered
//
.list-bordered {  
  @include spacingX(30px);
  letter-spacing: .05em;

  li {
    position: relative;
    display: inline-block;
    padding-top: 3px;
    padding-bottom: 3px;
  }

  a {
    color: inherit;

    &:hover {
      color: $primary;
    }
  }
}

%context-dark {
  .list-bordered {
    color: rgba($white, .5);
    
    li:not(:last-child) {
      &::after {
        background: rgba($white, .2);
      }
    }
  }
}

// Medium ≥576px
@include media-breakpoint-up(sm) {
  .list-bordered {
    li:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        right: 0;
        width: 1px;
        height: 13px;
        background: $gray-700;
      }
    }
  }
}

// Extra large ≥1200px
@include media-breakpoint-up(xl) {
  .list-bordered {
    @include spacingX(40px);
  } 
} 

//
// List Tags
//
.list-tags {
  @include group(10px);
  font-size: 12px;
  font-weight: 700;
  letter-spacing: .05em;
  text-transform: uppercase;
  color: rgba($gray-800, .7);
  
  a {
    display: block;
    color: inherit;
    
    &:hover {
      color: $primary;
    }
  }
}


// 
// List inline bordered
//
.list-inline-bordered {
  @include spacing(30px, 6px);
  
  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
  }
  
  .icon {
    position: relative;
    top: -1px;
    display: inline-block;
    vertical-align: middle;
    font-size: 20px;
    line-height: 1;
    color: $primary;
    margin-right: 4px;
  }
  
  a {
    color: $gray-800;
    
    &:hover {
      color: $primary;
    }
  }
}

* + .list-inline-bordered { margin-top: 20px; }

@include media-breakpoint-up(sm) {
  .list-inline-bordered {
    li {
      &:nth-last-child(n + 2) {
        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate3d(0, -50%, 0);
          width: 1px;
          height: 19px;
          background: $gray-200;
        }
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  .list-inline-bordered {
    @include spacingX(36px);
  }
  
  * + .list-inline-bordered { margin-top: 28px; } 
}


//
// List Creative
//
.list-creative {
  text-align: left;
}

* + .list-creative {
  margin-top: 30px;
}

@include media-breakpoint-down(xs) {
  .list-creative {
    > li {
      padding: 30px 0;

      &:first-child { padding-top: 0; }
      &:last-child { padding-bottom: 0; }
    }

    > li + li {
      border-top: 1px solid $gray-100;
    }
  }
}

@include media-breakpoint-between(sm, md) {
  .list-creative {
    display: flex;
    flex-wrap: wrap;
    @include groupX(20px);

    > li {
      flex-grow: 1;
    }
  }
}

@include media-breakpoint-up(lg) {
  .list-creative {
    > li {
      padding: 30px 0;

      &:first-child { padding-top: 0; }
      &:last-child { padding-bottom: 0; }
    }

    > li + li {
      border-top: 1px solid $gray-100;
      border-left: 0;
    }
  }

  * + .list-creative { margin-top: 40px; }
}

//
// List Terms Medium
//

.list-terms-medium {
  position: relative;
  padding: 10px 0 10px 30px;

  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 0;
    bottom: 2px;
    width: 4px;
    background: $primary;
  }

  dt {
    font-weight: 700;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: $gray-800;

    &::after {
      content: ':';
    }
  }

  dd {
    letter-spacing: .05em;
  }

  a {
    color: $gray-600;

    &:hover {
      color: $primary;
    }
  }

  dt + dd { margin-top: 10px; }
}
