//
// Blog layout
//
.blog-layout {
  > * + * {
    position: relative;
    margin-top: 45px;
    
    &::before {
      content: '';
      position: absolute;
      top: -5px;
      left: 0;
      right: 0;
      border-top: 1px solid $gray-200;
    }
  }
}

.blog-layout__item {
  background: $white;
  box-shadow: $shadow-area-1;
  
  > * + * {
    border-top: 1px solid $gray-200;
  }
}

.blog-layout__aside {
  > * {
    margin-top: 35px;
  }
}

.blog-layout__item-header,
.blog-layout__item-main {
  padding: 15px;
}

.blog-layout__main-item + .blog-layout__main-item { margin-top: 40px; }

@include media-breakpoint-up(sm) {
  .blog-layout__item-header,
  .blog-layout__item-main {
    padding-left: 20px;
    padding-right: 20px; 
  }
 
  .blog-layout__item-header {
    padding-top: 20px;
    padding-bottom: 15px;
  }

  .blog-layout__item-main {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}

@include media-breakpoint-between(md, md) {
  .blog-layout__aside {
    columns: 2;
    break-inside: avoid;
    column-span: none;
    column-gap: 30px;
    
    > * {
      display: inline-block;
      width: 100%;
    }
  }
}

@include media-breakpoint-up(lg) {
  .blog-layout {
    display: flex;
    margin-left: - round($grid-gutter-width / 2);
    margin-right: - round($grid-gutter-width / 2);

    > * + * {
      margin-top: 0;
      &::before { display: none; }
    }
  }

  .blog-layout__main,
  .blog-layout__aside {
    padding-left: 15px;
    padding-right: 15px;
  }

  .blog-layout__main {
    width: (8 / 12) * 100%;
  }

  .blog-layout__aside {
    width: (4 / 12) * 100%;
    
    > * {
      &:first-child {
        margin-top: 0;
      }
    }
  }

  .blog-layout__main-item + .blog-layout__main-item { margin-top: 65px; }
}

@include media-breakpoint-between(lg, lg) {
  .blog-layout__main {
    .row {
      margin-left: -10px;
      margin-right: -10px;

      [class*='col'] {
        padding-left: 10px;
        padding-right: 10px;
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .blog-layout__item-header,
  .blog-layout__item-main {
    padding-left: 35px;
    padding-right: 35px;
  }

  .blog-layout__item-header {
    padding-top: 30px;
    padding-bottom: 25px;
  }

  .blog-layout__item-main {
    padding-top: 35px;
    padding-bottom: 35px;
  }
} 
