/*
*
* Tables custom
*/

$table-head-padding: 35px 24px;
$table-cell-padding: 17px 24px;
$table-condensed-cell-padding: 5px; 

$table-bg: $white;
$table-bg-accent: $gray-100;
$table-bg-hover: $primary;
$table-bg-active: $table-bg-hover;

$table-border-color: $gray-100;

.table {
  text-align: left;
  background: $table-bg;
  border-collapse: collapse;

  tr {
    transition: .5s;
  }

  th,
  td {
    color: $gray-700;
    background: $table-bg;
  }

  th {
    padding: $table-head-padding;
    font-family: $font-family-sec;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: .05em;
    background: $gray-200;
    white-space: nowrap;

    @include media-breakpoint-down(md) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  td {
    padding: $table-cell-padding;
    background-color: transparent;
  }

  tbody tr:first-child {
    td {
      border-top: 0;
    }
  }

  tr td {
    border-bottom: 1px solid $table-border-color;
  }

  tfoot {
    td {
      font-weight: 700;
    }
  }
}

* + .table-responsive { margin-top: 30px; }

@include media-breakpoint-up(md) {
  * + .table-responsive { margin-top: 40px; }
}

//
// Table Custom styles variants
//
.table.table-primary {
  thead th {
    color: $white;
    background: $primary;
    border: 0;
  }
  tbody tr:hover {
    background: $table-bg-accent;
  }
}


.table.table-gray-700 {
  thead th {
    color: $white;
    background: $gray-700;
    border: 0;
  }
  tbody tr:hover {
    background: $table-bg-accent;
  }
}

.table.table-bordered {
  tr {
    td:first-child { border-left: 0; }
    td:last-child { border-right: 0; }
  }
  td {
    border: 1px solid $table-border-color;
  }

  tbody > tr:first-of-type > td {
    border-top: 0;
  }
}

.table.table-striped {
  border-bottom: 1px solid $table-border-color;
  tbody tr:nth-of-type(odd) {
    background: transparent;
  }

  tbody tr:nth-of-type(even) {
    background: $table-bg-accent;
  }

  tbody td {
    border: 0;
  }

  tfoot td:not(:first-child) {
    border-left: 0;
  }
}

