/*
*
* Modal Custom
*/

// Reset
.modal-content {
	border-radius: 0;
	border: 0;
	box-shadow: 0 0 20px 0 rgba($black, 0.1);
}

.modal {
	.close {
		cursor: pointer;
		color: rgba($gray-800, .5);
		font-size: 20px;
		transition: .22s;
		
		&::before {
			content: '\ec35';
			font-family: 'Linearicons';
		}
		
		&:hover {
			color: $gray-800;	
		}
	}
}

.modal-header {
	padding: 0;
	border-bottom: 0;
}

.modal-body {
	padding: 0;
}

//
// Modal login register
//
.modal-login-register {
	.modal-content { 
		position: relative;
		padding: 30px 20px;
	}
	
	.modal-header {
		padding-right: 40px;
	}
	
	.close {
		position: absolute;
		right: 20px;
		top: 30px;
	}
	
	* + p { margin-top: 20px; }
	* + .form { margin-top: 20px; }
	* + .modal-body { margin-top: 27px; }
}

@include media-breakpoint-down(xs) {
	.modal-login-register {
		form .group {
			margin-left: 0;
			.button {
				display: block;
				width: 100%;
				margin-left: 0;
			}
		}
	}
}

@include media-breakpoint-up(md) {
	.modal-login-register {
		.modal-content {
			padding: 50px 40px 50px;
		}

		.close {
			top: 40px;
			right: 40px;
			font-size: 28px;
		}
	}
}

@include media-breakpoint-up(xl) {
	.modal-login-register {
		.modal-content {
			padding-top: 75px;
		}
	}
} 
