/*
*
* Links
*/

// Standard link style
a {
  transition: $transition-base;
  
  &,
  &:focus,
  &:active,
  &:hover {
    text-decoration: $link-decoration;
  }

  @include link($link-color, $link-hover-color);

  &[href*='tel'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover {
  color: $link-hover-color;
}

.link-press {
  color: $link-press-color;
}

// Styles
.link-default {
  color: $body-color;
  
  &:hover {
    color: $primary;
  }
}

%context-dark {
  .link-default {
    color: $white;

    &:hover {
      color: $primary;
    }
  }
}

.link-light {
  color: $gray-500;
  
  &:hover {
    color: $gray-800;
  }
}

%context-dark {
  .link-light {
    color: rgba($white, .3);
    
    &:hover {
      color: $white;
    }
  }
}

// Privacy Link
.privacy-link {
  display: inline-block;

  &:hover {
    color: $gray-800;
  }
}

* + .privacy-link { margin-top: 30px; }


// Link arrow
.link-arrow {
  display: inline-flex;
  align-items: center;
  letter-spacing: .05em;
  
  &, 
  &:active, 
  &:focus {
  	color: $gray-800;
  }
  
  &:hover {
    color: $primary;
  }
  
  &::after {
    position: relative;
    top: 1px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    content: '\f14f';
    font-family: 'Material Design Icons';
  }
}

* + .link-arrow { margin-top: 20px; }

%context-dark {
  .link-arrow {
    &, 
    &:active, 
    &:focus {
    	color: $primary;
    }
    
    &:hover {
      color: $white;
    }
  }
}

// Link Box
.link-box {
  position: relative;
  display: block;
  padding: 15px 25px 20px 25px;
  text-align: center; 
  
  > * + * { margin-top: 10px; }
  
  p {
    letter-spacing: .05em;
  }

  h4 + p,
  .heading-4 + p {
    margin-top: 5px;
  }
  
  &, 
  &:active, 
  &:focus, 
  &:hover {
    color: $body-color;
  }
  
  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    display: block;
    pointer-events: none;
    border: 0 solid $gray-300;
    height: 22px;
    transition: .22s;
  }
  
  &::before {
    top: 0;
    border-width: 1px 1px 0 1px;
  }
  
  &::after {
    bottom: 0;
    border-width: 0 1px 1px 1px;
  }
  
  &:hover {
    color: $body-color;
    
    .link-box__icon {
      color: $primary;
    }
    
    &::before { transform: translate3d(0, -10px, 0); }
    &::after { transform: translate3d(0, 10px, 0); }
  }
}

span.link-box__icon {
  display: block;
  font-size: 37px;
  line-height: 1;
  color: $gray-800;
  transition: $transition-base;
}

@include media-breakpoint-down(xs) {
  .link-box {
    max-width: 360px;
    margin-left: auto;
    margin-right: auto;
    
    p {
      max-width: 250px;
      margin-left: auto;
      margin-right: auto;
    }
  } 
}

// Medium ≥768px
@include media-breakpoint-up(sm) {
  .link-box {
    display: flex;
    padding: 15px 25px 20px 50px;
    flex-direction: row-reverse;
    align-items: center;
    max-width: 570px; 
    margin-left: auto;
    margin-right: auto;
    text-align: left;
     
    > * + * { margin-right: 50px; }
  }
  
  .link-box__main {
    flex-grow: 1;
  }
  
  .link-box__icon {
    flex-shrink: 0;
  }
}
