/*
*
* Buttons
*/

// Button General Styles

.button {
  position: relative;
  z-index: 0;
  overflow: hidden;
  display: inline-block;
  @include btn-size(17px, 25px, $input-btn-font-size, $input-btn-line-height);
  border: 2px solid;
  font-family: $btn-font-family;
  font-weight: $btn-font-weight;
  letter-spacing: $btn-letter-spacing;
  text-transform: $btn-text-transform;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  transition: .25s ease-in-out;
  

  @include media-breakpoint-up(sm) {
    padding-left: 30px;
    padding-right: 30px;
  }
  
  @include media-breakpoint-up(lg) {
    padding: $input-btn-padding-y $input-btn-padding-x;
  }
}

.button-block {
  display: block;
  width: 100%;
}

//
// Appearance style
//
.button-default {
  @include btn-variant($gray-300, $gray-700, $gray-700, $white, $primary, $primary);
}

.button-gray-100 {
  @include btn-variant($gray-800, $gray-100, $gray-100, $gray-800, darken($gray-100, 5%), darken($gray-100, 5%));
} 

.button-primary { 
  @include btn-variant($white, $primary, $primary, $white, $gray-700, $gray-700);
}

.button-color-2 {
  @include btn-variant($white, $color-2, $color-2, $white, $primary, $primary);
}

// Outline
.button-default-outline {
  @include btn-variant($gray-800, transparent, $gray-800, $white, $primary, $primary);
}

.button-ghost {
  border: 0;
  background-color: transparent;

  &:hover {
    color: $white;
    background: $primary;
  }

  &::before {
    background: $primary;
  }
}


// Social
.button-facebook {
  @include btn-variant($white, $facebook, $facebook, $white, $facebook-darken, $facebook-darken);
}

.button-twitter {
  @include btn-variant($white, $twitter, $twitter, $white, $twitter-darken, $twitter-darken);
}

.button-google {
  @include btn-variant($white, $google, $google, $white, $google-darken, $google-darken);
}

// Btn with shadow
.button-shadow {
  box-shadow: 0 9px 21px 0 rgba($gray-300, 0.15);

  &:hover {
    box-shadow: 0 9px 10px 0 rgba($gray-300, 0.15);
  }

  &:focus,
  &:active {
    box-shadow: none;
  }
}

//
// Button effects
//
.button-ujarak {
  position: relative;
  z-index: 0;
  transition: background .4s, border-color .4s, color .4s;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $primary;
    z-index: -1;
    opacity: 0;
    transform: scale3d(0.7, 1, 1);
    transition: transform 0.42s, opacity 0.42s;
    border-radius: inherit;
  }

  &,
  &::before {
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  }

  &:hover {
    transition: background .4s .4s, border-color .4s 0s, color .2s 0s;
    &::before {
      opacity: 1;
      transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    }
  }
}

//
// Button Sizes
// 

.button-xs {
  padding: 8px 17px;
}

.button-sm {
  @include btn-size($input-btn-padding-y-sm, $input-btn-padding-x-sm, 12px, $input-btn-line-height-sm);
}

.button-lg {
  @include btn-size($input-btn-padding-y-lg, $input-btn-padding-x-lg, 16px, $input-btn-line-height-lg);
  letter-spacing: .035em;
}

.button-xl {
  @include btn-size(28px, 65px, 16px, 28px);
  letter-spacing: .035em;

  @include media-breakpoint-up(xl) {
    padding-left: 80px;
    padding-right: 80px;
  }
}

//
// Button Shapes
// 

.button-circle {
  border-radius: 30px;
}

.button-round-1 {
  border-radius: 5px;
}

.button-round-2 {
  border-radius: 10px;
}


//
// Button Icon styles
//
.button {
  &.button-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    
    .icon {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      color: inherit;
      font-size: 1.55em;
      line-height: 1em;
    }

    &-left {
      .icon {
        padding-right: 11px;
      }
    }

    &-right {
      flex-direction: row-reverse;
      .icon {
        padding-left: 11px;
      }
    }

    &.button-link {
      .icon {
        top: 5px;
        font-size: 1em;
      }
    }

    &.button-xs {
      .icon {
        top: .05em;
        font-size: 1.2em;
        padding-right: 8px;
      }

      .button-icon-right {
        padding-left: 8px;
      }
    }
  }
}

.button-icon-only {
  padding: 10px 17px;
  .icon {
    font-size: 27px;
  }
}

//
// Bootstrap buttons
//

.btn-primary {
  border-radius: 3px;
  font-family: $btn-font-family;
  font-weight: $btn-font-weight;
  letter-spacing: .05em;
  text-transform: uppercase;
  transition: .33s;

  &,
  &:active,
  &:focus {
    color: $white;
    background: $primary;
    border-color: $primary;
  }

  &:hover {
    color: $white;
    background: $black;
    border-color: $black;
  }
}


//
// Custom types
//
.button-video {
  position: relative;
  display: block;
  width: 60px;
  height: 60px;
  overflow: hidden;
  border: 4px solid $gray-700;
  border-radius: 50%;
  transition: .33s;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 53%;
    transform: translate3d(-50%, -50%, 0);
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 0 10px 18px;
    border-color: transparent transparent transparent $primary;
  }

  &:hover {
    background: $gray-700;
  }
}

@include media-breakpoint-up(md) {
  .button-video {
    width: 86px;
    height: 86px;
  }
}

%context-dark {
  .button-video {
    border-color: $white;

    &:hover {
      background: $white;
    }
  }
}
