//
// Nav custom
// 

// 
// Table of Contents:
// 
// Tabs Line 
// Tabs Corporate
//

// Line style
$tabs-line-border: $gray-300;
$tabs-line-background: transparent;
$tabs-line-color: rgba($gray-800, 600);
$tabs-line-active-color: $primary;

// Corporate style
$tabs-corporate-color: $body-color;
$tabs-corporate-background: transparent;
$tabs-corporate-active-color: $white;
$tabs-corporate-active-background: $primary;
$tabs-corporate-border: #e5e7e9;

// Light style
$tabs-light-color: $body-color;
$tabs-light-border: $gray-200;
$tabs-light-active-color: $primary;
$tabs-light-border: #e5e7e9;

// Base styles
.tabs-custom {
  text-align: left;

  .nav-tabs {
    font-size: 0;
    line-height: 0;
    word-spacing: 0;
    border: 0;

    &:before,
    &:after {
      display: none;
    }
  }

  .nav-item {
    float: none;
    border: 0;
    cursor: pointer;
    transition: .33s all ease;
  }

  .nav-link {
    margin: 0;
    border-radius: 0;
    border: 0;
  }

  .nav-link.active {
    cursor: default;
    border: 0;
  }
}

.nav {
  display: block;
}

.tab-content > .tab-pane {
  display: block;
  visibility: hidden;
  height: 0;
  overflow: hidden;
}

.tab-content > .active {
  visibility: visible;
  height: auto;
  overflow: visible;
}

// Offsets
* + .tabs-horizontal.tabs-corporate {
  margin-top: 35px;
}

* + .tabs-vertical.tabs-corporate {
  margin-top: 40px;
}

@include media-breakpoint-up(md) {
  * + .tabs-horizontal.tabs-corporate {
    margin-top: 50px;
  }

  * + .tabs-vertical.tabs-corporate {
    margin-top: 60px;
  }
}

@include media-breakpoint-up(xl) {
  * + .tabs-vertical.tabs-corporate {
    margin-top: 80px;
  }
}

// Tabs line
.tabs-line {
  .nav-tabs {
    position: relative;
  }

  .nav-link {
    font-family: $font-family-sec;
    font-size: 17px;
    font-weight: 500;
    line-height: 1.4;
    text-align: center;
    vertical-align: middle;
  }

  p {
    letter-spacing: .05em;
  }

  * + .tab-content {
    margin-top: 20px;
  }

  @include media-breakpoint-down(md) {
    .nav-tabs {
      max-width: 100%;
      border: 1px solid $tabs-line-border;
    }

    .nav-item {
      margin: -1px;

      &:first-child {
        border-top: 1px solid $tabs-line-border;
      }
    }

    .nav-link {
      padding: 7px 10px;
      color: $tabs-line-color;
      background: $tabs-line-background; 
      border-bottom: 1px solid $tabs-line-border;
    }

    .nav-link:hover,
    .nav-link.active {
      color: $white;
      background: $primary;
      border-color: $primary;
      border-bottom: 1px solid $tabs-line-border;
    }
  }
}

* + .tabs-line { margin-top: 30px; }

// Tabs corporate style
.tabs-corporate {
  .nav-tabs {
    position: relative;
    border: 1px solid $tabs-corporate-border;
  }

  .nav-item {
    margin: -1px;
  }

  .nav-link {
    padding: 10px 10px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 700;
    line-height: 1.4;
    color: $tabs-corporate-color;
    background: $tabs-corporate-background;
    border-bottom: 1px solid $tabs-corporate-border;
    text-align: center;
    vertical-align: middle;

    &:first-child {
      border-top: 1px solid $tabs-corporate-border;
    }
  }

  .nav-link:hover,
  .nav-link.active {
    color: $tabs-corporate-active-color;
    background: $tabs-corporate-active-background;
    border-color: $tabs-corporate-active-background;
  }

  .tab-content {
    padding: 30px 0 0;
  }
}

* + .tabs-light { margin-top: 40px; }

// Tabs light
.tabs-light {
  .nav-tabs {
    position: relative;
  }

  .nav-link {
    font-family: $font-family-sec;
    font-size: 17px;
    font-weight: 500;
    line-height: 1.4;
    text-align: center;
    vertical-align: middle;
  }

  p {
    letter-spacing: .05em;
  }

  * + .tab-content {
    margin-top: 20px;
  }

  @include media-breakpoint-down(md) {
    .nav-tabs {
      max-width: 100%;
      border: 1px solid $tabs-line-border;
    }

    .nav-item {
      margin: -1px;

      &:first-child {
        border-top: 1px solid $tabs-line-border;
      }
    }

    .nav-link {
      padding: 7px 10px;
      color: $tabs-line-color;
      background: $tabs-line-background;
      border-bottom: 1px solid $tabs-line-border;
    }

    .nav-link:hover,
    .nav-link.active {
      color: $white;
      background: $primary;
      border-color: $primary;
      border-bottom: 1px solid $tabs-line-border;
    }
  }
}

@include media-breakpoint-up(sm) {
  //
  // Horizontal tabs
  // 
  .tabs-horizontal {
    &.tabs-corporate {
      .nav-tabs {
        position: relative;
        width: 100%;
        display: block;
        @include spacing(10px);
        text-align: center;
        border: 0;
        will-change: transform;
      }

      .nav-item {
        display: inline-block;
        will-change: transform; 
        pointer-events: none;
      }

      .nav-link {
        display: block;
        position: relative;
        overflow: hidden;
        z-index: 0;
        min-width: 130px;
        font-size: 14px;
        padding: 20px 30px;
        pointer-events: auto;
        border: 2px solid $tabs-corporate-border;        
      }

      .nav-link.active,
      .nav-link:hover {
        color: $tabs-corporate-active-color;
        border-color: $tabs-corporate-active-background;
        background: $tabs-corporate-active-background;
      }
    }

    &.tabs-line {
      .nav-tabs {
        display: flex;
        justify-content: space-between;
        border: 0;
        border-bottom: 1px solid $tabs-line-border;
        text-align: left;
      }

      .nav-item {
        position: relative;
        flex: 1 1 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 18px;
        vertical-align: middle;
        border: 0;
        
        &::after {
          content: '';
          display: inline-block;
          height: 25px;
          width: 1px;
          background: $tabs-line-border;
          vertical-align: middle;
          margin: auto;
        }
      }
      
      .nav-item:last-child::after {
        display: none;
      }

      .nav-link {
        position: relative;
        padding: 0;
        background-color: transparent;
        color: $tabs-line-color;
        border: 0;
      }

      .nav-link.active,
      .nav-link:hover {
        color: $tabs-line-active-color;
      }

      * + .tab-content {
        margin-top: 30px;
      }
    }
  }

  // 
  // Vertical tabs
  // 
  .tabs-vertical {
    display: flex;
    align-items: flex-start;

    .nav-tabs {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex-shrink: 0;
      max-width: 50%;
    }

    .nav-item {
      border: 0;
      width: 100%;
      text-align: left;
    }

    .nav-link.active,
    .nav-link:hover {
      box-shadow: 2px 2px 20px 0 rgba(#1e1e1e, .1);
    }

    .tab-content {
      flex-grow: 1;
    }

    // Tabs corporate
    &.tabs-corporate {
      .nav-tabs {
        width: auto;
        min-width: 260px;
        border: 0;
      }

      .nav-item {
        margin: 0;
      }

      .nav-link {
        position: relative;
        padding: 20px 30px;
        border: 0;
        overflow: hidden;
        text-align: left;
      }

      .nav-item + .nav-item {
        margin-top: 2px;
      }

      .tab-content {
        padding: 0 0 0 30px;
      }
    }
  }
}

@include media-breakpoint-up(md) {
  //
  // Horizontal tabs
  //
  .tabs-horizontal {
    &.tabs-light {
      .nav-tabs {
        border: 0;
        border-bottom: 6px solid $tabs-light-border;
        text-align: center;
      }

      .nav-item {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 0;
      }

      .nav-item:last-child::after {
        display: none;
      }

      .nav-link {
        position: relative;
        padding: 0 30px 30px;
        background-color: transparent;
        color: $tabs-line-color;
        border: 0;
        margin-bottom: -6px;

        &::after {
          content: '';
          position: absolute;
          right: 0;
          bottom: 0;
          width: 0;
          height: 6px;
          background: $tabs-line-active-color;
          pointer-events: none;
          transition: .22s;
        }
      }

      .nav-link.active,
      .nav-link:hover {
        &::after {
          left: 0;
          right: auto;
          width: 100%;
        }
      }

      * + .tab-content {
        margin-top: 30px;
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  //
  // Horizontal tabs
  // 
  .tabs-horizontal {

    // Tabs Corporate
    &.tabs-corporate {
      .tab-content {
        padding: 40px 0 0;
      }
    }
  }

  // 
  // Vertical tabs
  // 
  .tabs-vertical {

    // Tabs corporate
    &.tabs-corporate {
      .tab-content {
        padding: 0 0 0 45px;
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .tabs-horizontal {
    &.tabs-light {
      .nav-link {
        padding-left: 45px;
        padding-right: 45px;
      }
    }
  }
}
