/*
*
* Owl Carousel
*/

.owl-carousel .animated {
  animation-duration: 1000ms;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  animation-name: fadeOut;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  transition: height 500ms ease-in-out;
}

/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel .owl-refresh .owl-item {
  display: none;
}

.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  font: 400 40px/80px 'FontAwesome';
  cursor: pointer;
  z-index: 1;
  transition: scale 100ms ease;
  &:before {
    content: '\f144';
  }
}

.owl-carousel .owl-video-play-icon:hover {
  transform: scale(1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}

/*
 * Owl Navigation
 */

.owl-nav {
  &.disabled {
    display: none !important;
  }
}

.owl-prev, 
.owl-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font: 400 20px/20px 'Material Design Icons';
  color: $gray-800;
  cursor: pointer;

  &:hover {
    color: $primary;
  }
}

.owl-prev {
  left: 0;

  &::before {
    content: '\f14a';
  }
}

.owl-next {
  right: 0;

  &::before {
    content: '\f14f';
  }
}

/*
 * Owl Pagination
 */

.owl-dots {
  text-align: center;
  margin-top: 10px;
  
  &.disabled {
    display: none !important;
  }
}

.owl-dot {
  position: relative;
  display: inline-block;
  width: 17px;
  height: 17px;
  border: 3px solid $gray-300;
  border-radius: 50%;
  margin-left: 5px;
  margin-right: 5px;
  text-align: center;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  transition: .2s;
  
  &:only-child {
    display: none;
  }

  &:hover,
  &:focus {
    border-color: $primary;
  }

  &.active {
    border-color: $primary;
  }
}

* + .owl-carousel { margin-top: 30px; }

// Custom 1
.owl-carousel-custom-1 {
  .owl-dots {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 0;
  }
  
  .quote-classic__meta {
    padding-right: 40px;
  }
}

* + .owl-carousel-custom-1 { margin-top: 30px; }

.owl-carousel[data-animation-in='fadeIn'][data-animation-out='fadeOut'] {
  .owl-animated-in {
    animation-duration: 1s;
  }
  
  .owl-animated-out {
    animation-duration: .2s;
  }
}

// Custom 2
.owl-carousel-custom-2 {
  padding-right: 50px;

  .owl-carousel-custom-2__nav {
    position: absolute;
    top: 50%;
    transform: translate3d(0, -50%, 0);
    right: 0;
  }
  
  .owl-nav.disabled {
    display: block !important;  
  }
  
  .owl-prev,
  .owl-next {
    position: static;
    top: 0;
    transform: none;
    padding: 20px 0;
    width: 32px;
    transition: .22s;
    
    
    &::before {
      font-family: 'Material Design Icons';
      font-size: 28px;
    }
  }
  
  .owl-prev {
    vertical-align: top;
    &::before {
      content: '\f156';
    }
  }
  
  .owl-next {
    vertical-align: bottom;
    &::before {
      content: '\f144';
    }
  }
  
  .owl-prev + .owl-next {
   border-top: 1px solid $gray-200; 
  }
}


//
// Owl outer navigation
//
.owl-navigation-outer {
  .owl-prev, 
  .owl-next {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    transform: none;
    left: auto;
    right: auto;
    top: auto;
    user-select: none;
  }

  .owl-prev + .owl-next {
    margin-left: 10px;
  }
}

//
// Owl Carousel & Post Classic
//
.owl-carousel-post-classic {
  .owl-stage-outer {
    padding: 16px;
    margin: -16px;
  }
}
