/*
*
* Form styles
*/

// RD Form
.form {
  position: relative;
  text-align: left;

  .button {
    padding-top: 12px;
    padding-bottom: 12px;
    min-height: $form-input-height;
  }
  
  .form-inline + .form-wrap { margin-top: 15px; }
}

.form-recaptcha {
  @include media-breakpoint-up(md) {
    [class*='col'] {
      display: inline-block;
    }

    .col-12 {
      max-width: 668px;
    }
    .col-md-6 {
      max-width: 334px;
    }
  }
}

* + .form { margin-top: 20px; }

input:-webkit-autofill ~ label,
input:-webkit-autofill ~ .form-validation {
  color: $gray-800 !important;
}

//
// Form wrap
//
.form-wrap {
  position: relative;

  &.has-error {
    .form-input {
      border-color: $form-feedback-invalid-color;
    }
  }

  &.has-focus {
    .form-input {
      border-color: $form-feedback-focus-color;
    }
  }
}

.form-equals-mark {
  padding-left: 10px;
  padding-right: 10px;
  font: 300 30px/30px $font-family-sec;
  color: $gray-800;
}

// Form wrap offset
.form-wrap + * { margin-top: 20px; }

// Form input
.form-input,
.form-info {
  display: block;
  width: 100%;
  min-height: $form-input-height;
  padding: $form-input-padding-vertical $form-input-padding-horizontal;
  font-size: $form-input-font-size;
  font-weight: $form-input-font-weight;
  line-height: $form-input-line-height;
  letter-spacing: .05em;
  color: $form-input-color;
  background-color: $form-input-background;
  background-image: none;
  border-radius: $form-input-border-radius;
  -webkit-appearance: none;
  transition: .3s ease-in-out;

  @if ($form-input-border) {
    border: $form-input-border;
  }

  &:focus {
    outline: 0;
  }
}

.bg-gray-100 {
  .form-input,
  .form-info {
    border-color: $form-input-background;
  }
}

// Form textarea
textarea.form-input {
  height: $form-textarea-default-height;
  min-height: $form-textarea-default-min-height;
  max-height: $form-textarea-default-max-height;
  resize: vertical;
}

// Form labels
.form-label,
.form-label-outside {
  margin-bottom: 0;
  color: $body-color;
  font-weight: $form-input-font-weight;
}

.form-label {
  position: absolute;
  top: $form-input-height / 2;
  left: 0;
  right: 0;
  padding-left: $form-input-padding-horizontal;
  padding-right: $form-input-padding-horizontal;
  font-size: $form-input-font-size;
  font-weight: $form-input-font-weight;
  line-height: $form-input-line-height;
  letter-spacing: .05em;
  pointer-events: none;
  text-align: left;
  z-index: 9;
  transition: .25s;
  will-change: transform;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $form-input-color;
  }
}

// Form label out of the field
.form-label-outside {
  width: 100%;
  margin-bottom: 4px;

  @include media-breakpoint-up(md) {
    position: static;

    &,
    &.focus,
    &.auto-fill {
      transform: none;
    }
  }
}

[data-x-mode='true'] {
  .form-label {
    pointer-events: auto;
  }
}

//
// Form validation
//
.form-validation {
  position: absolute;
  right: 8px;
  top: 0;
  z-index: 11;
  margin-top: 2px;
  font-size: 9px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0;
  color: $form-feedback-invalid-color;
  transition: .3s;
}

.form-validation-left {
  .form-validation {
    top: 100%;
    right: auto;
    left: 0;
  }
}

// Form output
#form-output-global {
  position: fixed;
  bottom: 30px;
  left: 15px;
  z-index: 2000;
  visibility: hidden;
  transform: translate3d(-500px, 0, 0);
  transition: .3s all ease;

  &.active {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }

  @include media-breakpoint-up(sm) {
    left: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.2;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &.error {
    color: $form-feedback-invalid-color;
  }

  &.success {
    color: $form-feedback-valid-color;
  }
}

//
// Radio and Checkbox Custom
//
.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    left: 0;
    width: 14px;
    height: 14px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: $white;

    &::after {
      position: absolute;
      opacity: 0;
      transform: scale3d(.8, .8, .8);
      transition: .22s;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-inline-wrap { 
  @include group(10px);
}

.radio,
.radio-inline,
.checkbox,
.checkbox-inline {
  position: relative;
  user-select: none;
  input {
    @include sr-only;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
  transform: scale3d(1, 1, 1);
}

//
// Custom Radio Styles
//
.radio,
.radio-inline {
  padding-left: 25px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: .05em;
  text-transform: uppercase;
  color: rgba($gray-800, .7);

  .radio-custom-dummy {
    top: 0;
    left: 0;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 2px solid $primary;

    &::after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $primary;
      border-radius: inherit;
    }
  }
  
  input:checked ~ * {
    color: $gray-800;
  }
}

//
// Custom Checkbox Styles
//
.checkbox,
.checkbox-inline {
  padding-left: 28px;
  color: $gray-500;
  cursor: pointer;

  .checkbox-custom-dummy {
    left: 0;
    width: 18px;
    height: 18px;
    margin: 0;
    border: 1px solid $gray-200;

    &::after {
      content: '\f222';
      font-family: $mdi;
      position: absolute;
      top: -1px;
      left: -2px;
      font-size: 20px;
      line-height: 18px;
      color: $gray-800;
    }
  }
}

//
// Checkbox Toggle
//
.toggle-custom {
  padding-left: 60px;
  -webkit-appearance: none;

  &:checked ~ .checkbox-custom-dummy {
    &::after {
      background: $primary;
      transform: translate(20px, -50%);
    }
  }

  & ~ .checkbox-custom-dummy {
    $toggle-height: 20px;
    position: relative;
    display: inline-block;
    margin-top: -1px;
    width: 44px;
    height: $toggle-height;
    background: $form-input-background;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      display: inline-block;
      width: $toggle-height - 4px;
      height: $toggle-height - 4px;
      left: 0;
      top: 50%;
      background: $gray-400;
      transform: translate(4px, -50%);
      opacity: 1;
      transition: .22s;
    }
  }
}

//
// RD Form Inline
//
.form-inline {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  text-align: center;
  margin-bottom: -5px;

  > * {
    margin-top: 0;
    margin-bottom: 5px;
  }

  .form-wrap {
    display: flex;
    flex: 1;
    min-width: 185px;
  }

  .form-label {
    display: block;
  }

  .form-wrap-select {
    text-align: left;
  }

  .form-button {
    flex-shrink: 0;
    min-height: $form-input-height;

    .button {
      display: block;
      min-height: inherit;
    }
  }

  @media (max-width: 359px) {
    .form-button {
      width: 100%;
    }

    .button {
      display: block;
      width: 100%;
    }
  }

  @include media-breakpoint-up(sm) {
    .button {
      display: block;
    }
  }

  &.form-inline-centered {
    justify-content: center;
  }
}

//
// Form Inline 2
//
.form_inline-2 {
  .form__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    text-align: center;

    > * {
      margin-top: 0;
    }

    .form-wrap {
      flex: 1;
      min-width: 185px;
    }
  }

  .form-label {
    display: block;
  }

  .form-button {
    flex-shrink: 0;
    min-height: $form-input-height;

    .button {
      min-height: inherit;
    }
  }

  * + .form-wrap { margin-top: 15px; }

  @include media-breakpoint-between(lg, lg) {
    .form-button, 
    .button {
      width: 100%;
    }
    
    * + .form-button { margin-top: 8px; }
    * + .form-wrap { margin-top: 10px; }
  }
}

//
// Form Small
//
.form-sm {
  .form-input,
  .button {
    padding-top: 9px;
    padding-bottom: 9px;
    min-height: $form-input-sm-height;
  }

  .form-validation {
    top: -16px;
  }

  .form-label {
    top: $form-input-sm-height / 2;
  }

  * + .button { margin-top: 10px; }
}

//
// Form Large
//
.form-lg {
  .form-input,
  .form-info,
  .select2 .select2-selection--single .select2-selection__rendered {
    min-height: $form-input-lg-height - 2px;
    padding-top: round(($form-input-lg-height - $form-input-line-height) / 2) - 1px;
    padding-bottom: round(($form-input-lg-height - $form-input-line-height) / 2) - 1px;
  }

  .form-input,
  .form-label {
    padding-left: 20px;
    padding-right: 20px;
  }

  .form-button {
    min-height: $form-input-lg-height;
  }

  .form-label {
    top: round($form-input-lg-height / 2);
  }

  // Small <576px
  @include media-breakpoint-down(xs) {
    .button {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  // Medium ≥768px
  @include media-breakpoint-up(md) {
    .form-input,
    .form-label {
      padding-left: 35px;
      padding-right: 35px;
    }
  }
}

// 
// Form Centered
//
.form-centered {
  .form-input,
  .form-label {
    text-align: center;
  }
}

// Calculator
.form-calculator {
  display: flex;
  align-items: center;
  max-width: 100%;
  @include group-media(7px);
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  width: 100%;

  > * {
    margin-top: 0;
  }

  .form-input {
    min-width: 95px;
  }

  .form-input,
  .form-info {
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
    font: 300 20px $font-family-sec;
    line-height: 1;
    letter-spacing: .01em;
    text-align: center;
  }

  .form-info {
    min-height: 20px;
    padding: 0 5px;
    border: 0;
    max-width: 70px;
  }

  .select-primary ~ .select2-selection__rendered {
    padding-left: 20px;
    padding-right: 35px;
  }

  // Small <576px
  @include media-breakpoint-down(xs) {
    flex-wrap: wrap;
    > * {
      width: 90px;
    }

    .form-input {
      max-width: calc(100% - 150px);
    }
  }
}

// Medium ≥768px
@include media-breakpoint-up(md) {
  .form-calculator {
    font-size: 26px;
  }
}

* + .form-calculator { margin-top: 35px; }

// Mods
.form_light {
  .form-input,
  .form-label {
    color: $gray-800;
  }

  .form-input {
    border-color: transparent;
    background: $gray-200;
  }
}

%context-dark {
  .form_light {
    .form-input,
    .form-label {
      color: $white;
    }

    .form-input {
      background: rgba($white, .1);
    }
  }
}
