/*
*
* Offsets
*/

// Elements offset
* + p { margin-top: 15px; }

* + h1,
* + .heading-1 {
  margin-top: 5px;
}

* + h2,
* + .heading-2 {
  margin-top: 5px;
}

* + h3,
* + .heading-3 {
  margin-top: 5px;
}

// Headings


// Headings + Paragraph
h2 + p, 
.heading-2 + p { 
  margin-top: 16px;
}

h4 + p, 
.heading-4 + p { 
  margin-top: 14px;
}


// Paragraph + Headings
p + h2,
p + .heading-2 { 
  margin-top: 15px; 
}


// Tags + Tags
p + p { margin-top: 12px; }
img + p { margin-top: 15px; }
h3 + img { margin-top: 42px; }


// Classes
* + .row { margin-top: 40px; }
* + .big { margin-top: 12px; }
* + .lead { margin-top: 20px; }
* + .text-block { margin-top: 30px; }
* + .button { margin-top: 30px; }
* + .group { margin-top: 30px; }
* + .group-sm { margin-top: 25px; }
* + .group-xl { margin-top: 35px; }

* + .form { margin-top: 30px; }
* + .form.comment-form { margin-top: 30px; }

// Classes + Tags 
.row + h3,
.row + .heading-3 {
  margin-top: 80px; 
} 


// Classes + Classes
.text-extra-large + h2, .text-extra-large + .heading-2 { margin-top: 30px; }
.container + .container { margin-top: 35px; }
.row + .row { margin-top: 35px; }

// Medium ≥768px
@include media-breakpoint-up(md) {
  * + h2,
  * + .heading-2 {
    margin-top: 10px;
  }
}
  
@include media-breakpoint-up(lg) {
  * + h1,
  * + .heading-1 {
    margin-top: 14px;
  }

  * + h2,
  * + .heading-2 {
    margin-top: 17px;
  }

  h4 + p,
  .heading-4 + p {
    margin-top: 19px;
  }

  h2 + p,
  .heading-2 + p {
    margin-top: 27px;
  }

  * + .big { margin-top: 20px; }
  html * + .offset-top-1 { margin-top: 65px; }
}

// Media offsets
@include media-breakpoint-up(xl) {
  * + .row { margin-top: 50px; }
  * + .form { margin-top: 40px; }
  * + .group-xl { margin-top: 45px; }

  .container + .container { margin-top: 60px; }
  .row + .row { margin-top: 60px; }
}

@include media-breakpoint-up(xxl) {
  * + h3,
  * + .heading-3 {
    margin-top: 18px;
  }
}


// Range spacing
.row-0 {
  @include grid-offset(0px);
}

.row-5 {
  @include grid-offset(5px);
}

.row-15 {
  @include grid-offset(15px);
}

.row-20 {
  @include grid-offset(20px);
}

.row-30 {
  @include grid-offset(30px);
}

.row-40 {
  @include grid-offset(40px);
}

.row-50 {
  @include grid-offset(50px);
}

@include media-breakpoint-up(lg) {
  .row-md-30 {
    @include grid-offset(30px);
  }
}
