/*
* @subsection  RD Navbar Static
*/

.rd-navbar-static {
  display: block;
  
  // RD Navbar Nav
  //
  // RD Nav item 1-st level
  .rd-nav-item {
    display: inline-block;
    &.focus,
    &.opened {
      .rd-nav-link {
        //color: $rd-navbar-nav-hover-color;
        //background: $rd-navbar-nav-hover-background;
      }
      
      > .rd-navbar-submenu-toggle {
        color: $rd-navbar-nav-hover-color;
      }
    }
    
    &.active {
      .rd-nav-link {
        //color: $rd-navbar-nav-active-color;
        //background: $rd-navbar-nav-active-background;
      }
      
      > .rd-navbar-submenu-toggle {
        color: $rd-navbar-nav-active-color;
      }
    }
    
    &.focus > .rd-navbar-submenu-toggle,
    &.opened > .rd-navbar-submenu-toggle,
    .rd-nav-link:hover + .rd-navbar-submenu-toggle {
      &::before {
        transform: rotate(180deg);
      }
    }
    
    > .rd-navbar-submenu-toggle {
      margin-left: 4px;
      font-family: "Material Design Icons";
      font-size: 16px;
      cursor: pointer;
      
      &::before {
        position: relative;
        display: inline-block;
        transition: .22s;
        content: '\f236';
      }
      
      &:hover {
        color: $rd-navbar-nav-hover-color;
      }
    }
    
    // If Have submenu
    > .rd-navbar-submenu {
      margin-top: 20px;
    }
  }

  .rd-nav-item.rd-navbar-submenu {
    margin-right: -8px;
  }

  .rd-nav-item + .rd-nav-item {
    margin-left: $navbar-static-nav-indent;
  }
  
  // Rd Nav Link 1-st level
  .rd-nav-link {
    position: relative;
    display: inline-block;
    color: $rd-navbar-nav-color;
    font-size: 17px;
    
    transition: .25s;
    
    &:hover {
      //color: $rd-navbar-nav-hover-color;
    }
  }
  
  // RD Menu (dropdown and megamenu)
  .rd-menu {
    position: absolute;
    z-index: 15;
    display: block;
    margin-top: 22px;
    visibility: hidden;
    opacity: 0;
    text-align: left;
    border: $rd-navbar-border;
    transform: translate3d(0, 30px, 0);
    @extend %rd-navbar-transition;
  }

  // Megamenu list item
  .rd-dropdown-item + .rd-dropdown-item,
  .rd-megamenu-list-item + .rd-megamenu-list-item {
    margin-top: 10px;
  }
  
  // RD Dropdown 1-st level
  .rd-navbar-dropdown { 
    position: absolute;
    left: -25px;
    width: $rd-navbar-dropdown-width;
    padding: 25px; 
    background: $rd-navbar-dropdown-background;
    z-index: 5;
    
    .rd-navbar-dropdown {
      top: -1px;
      left: 100%;
      z-index: 2;
      margin-top: -15px;
      margin-left: 15px;
      transform: translate3d(30px, 0, 0);
  
      // If the menu is on the right edge window
      &.rd-navbar-open-left {
        left: auto;
        right: 100%;
        margin-left: 0;
        margin-right: 15px;
        transform: translate3d(-30px, 0, 0);
      }
    }
  }
  
  .rd-dropdown-item {
    &.focus,
    &.opened {
      .rd-dropdown-link {
        color: $rd-navbar-dropdown-item-active-color;
        //background: $rd-navbar-dropdown-item-active-background;
      }
    }
  }
  
  .rd-dropdown-link {
    color: $rd-navbar-dropdown-item-color;
    //background: $rd-navbar-dropdown-item-background;
    
    &:hover {
      color: $rd-navbar-dropdown-item-hover-color;
      //background: $rd-navbar-dropdown-item-hover-background;
    }
  }
  
  
  .rd-navbar-dropdown,
  .rd-megamenu-list {
    @include submenu-with-arrow;
  }
  
  [dir='rtl'] & {
    .rd-navbar-dropdown,
    .rd-megamenu-list {
      li {
        > a {
          &::before {
            content: '\f104';
          }
        }
      }
    }
  }
  
  
  // RD Megamenu
  .rd-navbar-megamenu {
    display: flex;
    left: 0;
    width: 100%;
    max-width: $rd-navbar-width;
    padding: 30px;
    background: $rd-navbar-megamenu-background;
  }
  
  // Megamenu item 1st level
  .rd-megamenu-item {
    flex-basis: 50%;
    flex-grow: 1;
    
    + .rd-megamenu-item {
      padding-left: 20px;
    }
  }
  
  // Megamenu title
  .rd-megamenu-title {
    padding-bottom: 10px;
    border-bottom: 1px solid $gray-200;
  }
  
  // Megamenu list link
  .rd-megamenu-list-link {
    color: $rd-navbar-megamenu-item-color;
    background: $rd-navbar-megamenu-item-background;
    
    &:hover {
      color: $rd-navbar-megamenu-item-hover-color;
      background: $rd-navbar-megamenu-item-hover-background;
    }
  }
  
  // Megamenu list offset
  * + .rd-megamenu-list {
    margin-top: 15px;
  }
  
  // RD all submenus
  .rd-navbar-submenu {
    &.focus,
    &.opened {
      > .rd-menu {
        opacity: 1;
        visibility: visible;
        transform: translate3d(0, 0, 0);
      }
    }
  }
  
  // RD navbar search
  .rd-navbar-search {
    &.active {
      .rd-search {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  
  .rd-search {
    position: absolute;
    top: 100%;
    right: 0;
    width: 270px;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    margin-top: 20px;
    z-index: 2;
  }
  
  // RD search offset
  * + .rd-navbar-search { margin-left: $navbar-static-nav-indent; }
  
  .rd-navbar-search-toggle {
    display: inline-flex;
    @include toggle-icons-via-rotation(32px, 32px, 26px, '\f43b', '\f24c', 'Material Design Icons', 'Material Design Icons');
    color: $rd-navbar-nav-color;
    
    &:hover {
      color: $rd-navbar-nav-hover-color;
    }
  }
  
  // RD navbar Clone
  &.rd-navbar--is-clone {
    display: block;
    transform: translate3d(0, -100%, 0);
    
    &.rd-navbar--is-stuck {
      transform: translate3d(0, 0, 0);
    }
  }
  
  // RD Navbar Stuck || Clone
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: $zindex-rd-navbar;
    background: $rd-navbar-background;
  }
  
  // RD Navbar States
  .rd-navbar--has-dropdown {
    position: relative;
  }
}
