/*
*
* Dividers
*/

hr {
  margin-top: 0;
  margin-bottom: 0;
  border-top: $hr-border-width solid $hr-border-color;
}

.divider {
  font-size: 0;
  line-height: 0;
  
  &::after {
    content: '';
    display: inline-block;
    width: 60px;
    height: 2px;
    background-color: $primary;
  }
}

//
// Divider Modern
//
.divider-modern {
  height: 1px;
  font-size: 0;
  line-height: 0;
  background: $gray-200;

  &::after {
    content: '';
    display: inline-block;
    width: 75px;
    height: 2px;
    background: $primary;
  }
}

* + .divider-modern { margin-top: 30px; }
.divider-modern + p { margin-top: 25px; }

@include media-breakpoint-up(lg) {
  * + .divider-modern { margin-top: 40px; }
  .divider-modern + p { margin-top: 35px; }
}