/*
*
* Select 2 v4
*/

// Style variables
$select-color: $white;
$select-background: $primary;
$select-border: 0;
$select-border-radius: $form-input-border-radius;

$select-padding-y: $form-input-padding-vertical;
$select-padding-x: $form-input-padding-horizontal;

$select-font-size: $form-input-font-size;
$select-line-height: $form-input-line-height;
$select-font-weight: $form-input-font-weight;
$select-height: $form-input-height;


$select-arrow: '\f236';
$select-arrow-color: $select-color;
$select-placeholder-color: $body-color;

// Droppdown
$select-highlight-color: $white;
$select-highlight-bg: $gray-700;

$select-selected-color: $white;
$select-selected-bg: lighten($primary, 15%);

$select-disabled-color: $gray-500;
$select-disabled-bg: $gray-100;
$select-search-border: $form-input-border;

.select2-container {
  box-sizing: border-box;

  display: inline-block;
  margin: 0;
  position: relative;
  vertical-align: middle;

  @import "select-2_includes/single";
  @import "select-2_includes/multiple";
}

@import "select-2_includes/dropdown";

.select2-close-mask {
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  min-height: 100%;
  min-width: 100%;
  height: auto;
  width: auto; 
  opacity: 0;
  z-index: 99;

  // styles required for IE to work

  background-color: $white;
  filter: alpha(opacity=0);
}

.select2-hidden-accessible {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

@import "select-2_includes/themes/default/layout";


// Custom
.select-primary ~ .select2 {
  .select2-selection__rendered {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4px 23px 4px 13px;
    border: 0;
    font-family: $font-family-trd;
    font-weight: 700;
    font-size: 14px;

    &, &:focus, &:active, &:hover {
      color: $white;
      background: $primary;
    }
  }

  .select2-selection__arrow { 
    right: 15px;
    top: 50%;
  }
  
  // Medium ≥768px
  @include media-breakpoint-up(md) {
    .select2-selection__arrow {
      top: 44.5%;
    }
  }
}

.select-primary-dropdown {
  z-index: 1776;
  background: $primary;
  font-family: $font-family-trd;
  font-size: 12px;
  font-weight: 700;

  .select2-results {
    display: block;
    padding: 0; 
  }

  .select2-highlighted {
    background: $gray-700;

    .select2-result-label {
      color: $white;
    }
  }

  .select2-results > li {
    border-radius: 0;
    text-align: left;
  }

  .select2-result-label {
    padding-left: 20px;
    padding-right: 15px;
    color: $white;
  }

  .select2-match {
    display: block;
    text-decoration: none;
  }

  &.select2-drop-active {
    white-space: normal;
    border-color: $gray-200;
  }
}