/*
*
* Counter
*/

//
// Counter Classic
//
.counter-classic {
  position: relative;
  text-align: center;
  color: $gray-700;

  display: block;
  max-width: 240px;
  padding: 30px 8px;
  margin-left: auto;
  margin-right: auto;
  backface-visibility: hidden;

  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    pointer-events: none;
    height: 33px;
    border: 1px solid $gray-200;
    transition: .22s cubic-bezier(0.07, 0.65, 0.45, 0.94);
    transform: translate3d(0, 0, 0);
  }

  &::before {
    top: 0;
    border-bottom: 0;
  }

  &::after {
    bottom: 0;
    border-top: 0;
  }

  &:hover {
    &::before {
      transform: translate3d(0, -12px, 0);
    }

    &::after {
      transform: translate3d(0, 12px, 0);
    }

    .counter-classic__title {
      color: $primary;
    }
  }
}

.counter-classic__title {
  display: inline-block;
  font-family: $font-family-sec;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: .025em;
  transition: .22s linear;
}

.counter-classic__main {
  font-family: $font-family-sec;
  font-size: 45px;
  font-weight: 700;
  line-height: 1.2;

  > * {
    display: inline;
    font: inherit;
  }

  .small {
    font-size: 28px;
  }

  .small_top {
    position: relative;
    top: .2em;
    vertical-align: top;
  }
}

.counter-classic__divider {
  font-size: 0;
  line-height: 0;

  &::after {
    content: '';
    display: inline-block;
    width: 80px;
    height: 2px;
    background: $primary;
  }
}

* + .counter-classic__title { margin-top: 10px; }
* + .counter-classic__main { margin-top: 20px; }
* + .counter-classic__divider { margin-top: 10px; }

@include media-breakpoint-up(sm) {
  .counter-classic {
    padding: 40px 8px;
  }

  .counter-classic__title {
    font-size: 16px;
  }
}

@include media-breakpoint-up(md) {
  .counter-classic__title {
    font-size: 18px;
  }
}

@include media-breakpoint-up(xl) {
  .counter-classic__main {
    font-size: 60px;

    .small {
      font-size: 36px;
    }
  }

  * + .counter-classic__main { margin-top: 30px; }
  * + .counter-classic__divider { margin-top: 15px; }
  * + .counter-classic__title { margin-top: 18px; }
}

// Context styling
%context-dark {
  .counter-classic {
    color: $white;

    &::before,
    &::after {
      border-color: rgba($white, .2);
    }
  }

  .counter-classic__title {
    color: rgba($white, .8);
  }

  .counter-classic__divider {
    &::after {
      background: $white;
    }
  }
}