/*
*
* Pagination custom
*/

.pagination {
  font-size: 0;
  line-height: 0;
  @include group(10px);
}

.page-item {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  font-weight: 700;
}

// Base styles
.page-link {
  display: inline-block;
  min-width: $pagination-padding-y*2 + $pagination-border-width*2 + $pagination-line-height;
  padding: $pagination-padding-y $pagination-padding-x;
  border: $pagination-border-width solid $pagination-border-color;
  font-size: $pagination-font-size;
  line-height: ($pagination-line-height / $pagination-font-size);
  background-color: $pagination-bg;
  color: $pagination-color;
  transition: $transition-base;
}

// Hover & Focus & Active State
.page-link {
  &:hover,
  &:focus,
  &:active {
    color: $pagination-hover-color;
    background-color: $pagination-hover-bg;
    border-color: $pagination-hover-border-color;
  }
}

// Active State
.page-item.active > .page-link {
  &,
  &:hover,
  &:focus,
  &:active {
    color: $pagination-active-color;
    background-color: $pagination-active-bg;
    border-color: $pagination-active-border-color;
  }
}

// Disabled State
.page-item.disabled > .page-link {
  &,
  &:hover,
  &:focus,
  &:active {
    color: $pagination-disabled-color;
    background-color: $pagination-disabled-bg;
    border-color: $pagination-disabled-border-color;
  }
}


.page-item-control {
  .icon::before {
    font-family: 'FontAwesome';
    font-size: 11px;
    line-height: $pagination-line-height;
  }
}

// Prev control
.page-item-control:first-child,
[dir='rtl'] .page-item-control:last-child {
  .icon::before {
    content: '\f053';
    margin-left: -1px;
  }
}

// Next control
.page-item-control:last-child,
[dir='rtl'] .page-item-control:first-child {
  .icon::before {
    content: '\f054';
    margin-right: -1px;
  }
}

//
// Pagination Box
//
.pagination-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
  color: rgba($gray-800, .8);
  font-family: $font-family-sec;
  font-size: 18px;
  line-height: 1.2;
  border-bottom: 1px solid $gray-300;
}

.pagination-box__control {
  color: $gray-800;
  
  &:hover {
    color: $primary;
  }
}

* + .pagination-box { margin-top: 30px; }

@include media-breakpoint-up(sm) {
  .pagination-box {
    font-size: 20px;
  }
}

@include media-breakpoint-up(md) {
  * + .pagination-box { margin-top: 50px; } 
}
