//
// Box Minimal
//
.box-minimal {
  max-width: 270px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  
  &:hover {
    .box-minimal__icon {
      &::before {
        transform: rotateY(180deg);    
      }
    }
  }
}

.box-minimal__icon {
  position: relative;
  z-index: 1;
  height: 76px;
  width: 83px;
  font-size: 44px;
  line-height: 70px;
  color: $gray-800;
  
  &::before {
    position: relative;
    transition: .45s ease-out;
  }
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-image: url(../images/rounded-hexagon.svg);
    background-size: cover;
  }
}

.box-minimal__divider {
  font-size: 0;
  line-height: 0;
  
  &::after {
    content: '';
    display: inline-block;
    width: 45px;
    height: 3px;
    background: $primary;
  }
}

.box-minimal-text {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}

* + .box-minimal { margin-top: 30px; }
* + .box-minimal__title { margin-top: 20px; }
* + .box-minimal__divider { margin-top: 20px; }

// Context styling
%context-dark {
  .box-minimal {
    p {
      color: $body-color;
    }
  }
}

//
// Box Inset
//
.box-inset-1 {
  padding: 40px 15px;
  max-width: 485px;
  margin-left: auto;
  margin-right: auto;
}

@include media-breakpoint-up(sm) {
  .box-inset-1 {
    padding: 100px 15px;
    max-width: 570px;
  }
}

@include media-breakpoint-up(lg) {
  .box-inset-1 {
    padding-left: 30px;
    padding-right: 30px;
  }
}

// Extra large ≥1200px
@include media-breakpoint-up(xl) {
  .box-inset-1 {
    max-width: 495px;
    margin-left: auto;
    margin-right: 0;
  }
}

// 2
.box-inset-2 {

}

@include media-breakpoint-up(md) {
  .box-inset-2 {
    padding: 0 15px;
  }
}

@include media-breakpoint-up(lg) {
  .box-inset-2 {
    padding: 0 0 0 50px;
  }
}

@include media-breakpoint-up(xl) {
  .box-inset-2 {
    padding: 0 70px;
  }
}

// 3
.box-inset-3 {
  // Extra large ≥1200px
  @include media-breakpoint-up(xl) {
    padding-left: 70px; 
  } 
}

// 4 
.box-inset-4 {
  @include media-breakpoint-up(md) {
    padding-right: 90px; 
  }
  
  @include media-breakpoint-up(lg) {
    padding-right: 10px; 
  }
  
  @include media-breakpoint-up(xl) {
    padding-right: 70px;
  }
}

// 
// Box Lite
// 
.box-lite {
  text-align: center;

  &:hover {
    .box-lite__icon {
      color: $white;
      border-color: $primary;
    }
    
    .box-lite__icon::after {
      opacity: 1;
      visibility: visible;
      transform: scale3d(1, 1, 1);
    }
  }
}

a.box-lite { display: block; }

.box-lite__icon {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  font-size: 32px;
  color: $gray-800;
  border: 1px solid $gray-200;
  transition: .22s;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    transform: scale3d(.9, .9, .9);
    background: $primary;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: .22s;
  }
}

.box-lite__title {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: $gray-800;
}

@media (min-width: 480px) {
  .box-lite__icon {
    width: 77px;
    height: 77px;
  }

  .box-lite__title {
    font-size: 14px;
  }
}

* + .box-lite__title { margin-top: 10px; }

//
// Box Elegant
//
.box-elegant {
  max-width: 220px;
  margin-left: auto;
  margin-right: auto;
}

.box-elegant__header {
  display: flex;
  align-items: center;
  @include groupX(20px);
  
  > * {
    margin-top: 0;
  }
}

.box-elegant__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  padding: 10px;
  border: 2px solid $primary;
  border-radius: 50%;
  font-size: 24px;
  color: $gray-800;
}

.box-elegant__title {
  font-family: $font-family-sec;
  font-size: 20px;
  line-height: 1.2;
  color: $gray-800;
}

.box-elegant__text {
  font-size: 17px;
  line-height: (22 / 17);
}

%context-dark {
  .box-elegant__icon { 
    color: $white;  
  }
  
  .box-elegant__title {
    color: $white;
  }

  .box-elegant__text {
    color: rgba($white, .8);
  }
}

* + .box-elegant__text { margin-top: 22px; }

@include media-breakpoint-up(sm) {
  .box-elegant__icon {
    width: 60px;
    height: 60px;
    border-width: 3px;
  }

  .box-elegant__title {
    font-size: 24px;
  }
}

@include media-breakpoint-between(md, md) {
  .box-elegant__header {
    @include groupX(15px);
  }
  
  .box-elegant__icon {
    width: 50px;
    height: 50px;
    border-width: 2px;
    font-size: 18px;
  }
  
  .box-elegant__title {
    font-size: 22px;
  }

  .box-elegant__text {
    font-size: 14px;
    line-height: 1.5;
  }
}

@include media-breakpoint-up(xl) {
  .box-elegant {
    margin-left: 0;
  }
  .box-elegant__title {
    font-size: 30px;
  } 
} 


// 
// Box Lily
//
.box-lily {
  position: relative;
  display: block;
  text-align: center;
  padding: 25px 0;
  backface-visibility: hidden;
  
  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    pointer-events: none;
    height: 33px;
    border: 1px solid $gray-200;
    transition: .22s;
    transform: translate3d(0, 0, 0);
  }
  
  &::before {
    top: 0;
    border-bottom: 0;
  }
  
  &::after {
    bottom: 0;
    border-top: 0;
  }
  
  &:hover {
    &::before {
      transform: translate3d(0, -12px, 0);
    }
    
    &::after {
      transform: translate3d(0, 12px, 0);
    }
    
    .box-lily__title {
      color: $primary;
    }
  }
}

.box-lily__icon {
  font-size: 40px;
  line-height: 1;
  color: $primary;
}

.box-lily__title {
  font-family: $font-family-sec;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
  letter-spacing: .025em;
  color: $gray-800;
  transition: .3s;
}

* + .box-lily__title { margin-top: 12px; }

@include media-breakpoint-up(md) {
  .box-lily {
    padding: 35px 20px;
  }
  .box-lily__icon {
    font-size: 66px;
  }
}


@include media-breakpoint-up(lg) {
  .box-lily {
    padding: 55px 20px;
  }

  .box-lily__title {
    font-size: 20px;
  }

  * + .box-lily__title { margin-top: 30px; }
}
