/*
*
* Sections
*/

//
// Section Spacing
// 
.section-xs {
  padding: 25px 0;
}

.section-sm,
.section-md,
.section-lg,
.section-xl {
  padding: 50px 0;
}

// Section collapse
.section-collapse + .section-collapse {
  padding-top: 0;
}

.section-collapse:last-child {
  padding-bottom: 0;
}

// Section with bottom zero padding
html [class*='section-'].section-bottom-0 {
  padding-bottom: 0;
}

@include media-breakpoint-down(md) {
  section.section-sm,
  section.section-md,
  section.section-lg,
  section.section-xl,
  section.section-xxl {
    &:first-of-type {
      padding-top: 40px;
    }
  }
}

@include media-breakpoint-up(md) {
  .section-sm {
    padding: 50px 0;
  }

  .section-md {
    padding: 60px 0;
  }

  .section-lg {
    padding: 75px 0;
  }
}

@include media-breakpoint-up(lg) {
  .section-sm {
    padding: 60px 0;
  }

  .section-md {
    padding: 70px 0;
  }

  .section-lg {
    padding: 85px 0;
  }
}

@include media-breakpoint-up(xl) {
  .section-sm {
    padding: 75px 0;
  }
  
  .section-md {
    padding: 85px 0 100px;
  }

  .section-lg {
    padding: 100px 0;
  }
}

//
// Custom sections
//

// Section single
.section-single {
  display: flex;
  text-align: center;

  p {
    margin-left: auto;
    margin-right: auto;
  }

  * + .rights { margin-top: 35px; }
  * + .countdown-wrap { margin-top: 35px; }
  .countdown-wrap + * { margin-top: 35px; }

  @include media-breakpoint-down(xs) {
    .group {
      .button {
        display: block;
      }
    }
  }

  @include media-breakpoint-up(lg) {
    * + .rights { margin-top: 60px; }
    .countdown-wrap + * { margin-top: 35px; }
  }

  .rd-mailform-wrap {
    max-width: 670px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }

  * + .rd-mailform-wrap {
    margin-top: 20px;
  }
}

.section-single-header {
  padding: calc(1em + 3vh) 0 calc(1em + 2vh);
}

.section-single-main {
  padding: calc(1em + 4vh) 0;
}

.section-single-footer {
  padding: calc(1em + 2vh) 0 calc(1em + 3vh);
}

.section-single-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 100vh;

  > * {
    width: 100%;
  }
}

@supports (display: grid) {
  .section-single-inner {
    display: grid;
    justify-items: center;
    align-content: space-between;
    grid-template-columns: 1fr;
  }
}

.section-single.bg-gray-700.bg-image {
  h6,
  .heading-6 {
    color: rgba($white, .75);
  }
}

//
// Custom
//
.section-1 {
  position: relative;
  z-index: 1;

  .section-inner {
    position: absolute;
    top: 15px;
    left: 0;
    right: 0;
    bottom: 15px;
  }

  .section-svg__shape {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    min-width: 100%;
    min-height: 100%;
    max-height: 100%;
    pointer-events: none;
    z-index: 0;
  }
}

//
// Section elements
//
.section__header {
  position: relative;
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  $offset: 20px;
  padding-bottom: 15px;
  margin-bottom: -$offset;
  transform: translateY(-$offset);
  text-align: left;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    border-bottom: 1px solid #dddfe7;
  }

  > *:first-child {
    flex-grow: 1;
    padding-right: 20px;
  }

  > * {
    margin-top: $offset;
  }
}

//
// Section overlays
//
// Overlays
[class*='section-overlay'] {
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    z-index: 1;
  }

  > * {
    position: relative;
    z-index: 1;
  }
}

// Overlays
.section-overlay-1 {
  &::before {
    background-image: linear-gradient(-34deg, rgb(44, 53, 71) 0%, rgb(80, 105, 138) 100%);
    opacity: 0.569;
  }
}

.section-overlay-2 {
  &::before {
    background: $color-1;
    opacity: 0.95;
  }
}

.section-overlay-3 {
  &::before {
    background: $gray-700;
    opacity: 0.95;
  }
}

.section-overlay-4 {
  &::before {
    background: $color-6;
    opacity: 0.5;
  }
}

.section-overlay-5 {
  &::before {
    background: $color-6;
    opacity: 0.6;
  }
}

.section-overlay-6 {
  &::before {
    background: $color-6;
    opacity: 0.7;
  }
}
