/*
*
* Snackbars
*/


.snackbars {
  padding: 9px 16px;
  margin-left: auto;
  margin-right: auto;
  color: $white;
  text-align: left;
  background-color: $gray-800;
  border-radius: 0; 
  box-shadow: $shadow-area-1;

  font-size: 14px;

  .icon-xxs {
    font-size: 18px;
  }

  // padding on text
  p span:last-child {
    padding-left: 14px;
  }

  // snackbar icon on the left side
  &-left {
    display: inline-block;
    margin-bottom: 0;
  }

  // snackbar link or button on the right side
  &-right {
    display: inline-block;
    float: right;
    text-transform: uppercase;

    &:hover {
      text-decoration: underline;
    }
  }

  @include media-breakpoint-up(md) {
    max-width: 540px;
    padding: 15px 25px;
    font-size: 16px;
  }
}

