//
// Comments
//

.comment {
  
}

.comment-group {
  .comment-group {
    padding-left: 20px;

    .comment-group {
      padding-left: 10px;
    }
  }
}

.comment__aside {
  position: relative;
  z-index: 1;
  padding-left: 20px;
  margin-bottom: -30px;
}

.comment__main {
  position: relative;
  padding: 50px 25px 20px 20px;
  background: $gray-100;
  text-align: left;
}

.comment__header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  @include spacing(20px, 5px);
}

.comment__header-group {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  @include spacing(20px, 3px);
}

.comment__image {
  max-width: 100px;
  border-radius: 50%;
}

.comment__title {
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: .025em;
  text-transform: uppercase;
  color: $black;
}

.comment__time {
  font-weight: 500;
  color: rgba($gray-800, .8);
  
  .icon {
    color: $gray-500; 
  }
  
  > * {
    display: inline-block;
    vertical-align: middle;
  }
  
  > * + * {
    margin-left: 8px;
  }
}

.comment__link {
  color: $gray-800;
}

.comment__list {
  position: relative;
  top: -2px;
  font-size: 13px;
  font-weight: 500;
  color: $gray-800;
  @include spacing(12px, 10px);
  
  > li { 
    display: inline-block;
    vertical-align: middle;
 
    * { vertical-align: middle; }
    
    * + * {
      margin-left: 5px;
    }
    
    > a {
      transition: color .22s;
      
      &:hover {
        &, .icon {
          color: $primary; 
        }
      }
    }
  }
  
  .icon {
    margin-top: 1px;
    font-size: 16px;
    transition: color .22s;
  }

  .mdi-comment-outline {
    margin-top: 2px;
  }
}

.bg-gray-100 {
  .comment__main {
    background: $white;
  }
}

* + .comment__text { margin-top: 15px; }
* + .comment__footer { margin-top: 14px; }
* + .comment-group { margin-top: 30px; }
.comment + * { margin-top: 25px; }
.comment-group + .comment-group { margin-top: 30px; }

@include media-breakpoint-up(sm) {
  .comment {
    display: flex;
    align-items: flex-start;
    text-align: left;

    > * + * {
      margin-left: 20px;
    }
  } 
  
  .comment__aside {
    padding-top: 13px;
    padding-left: 0;
  }
  
  .comment__main {
    padding-top: 20px;
    &::before {
      content: '';
      position: absolute;
      left: -10px;
      top: 15px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 10px 17px 0;
      border-color: transparent $gray-100 transparent transparent;
    }
  }

  .comment__text {
    padding-right: 30px;
  }
  
  .bg-gray-100 {
    .comment__main {
      &::before {
        border-color: transparent $white transparent transparent;
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .comment-group {
    max-width: 850px;
    .comment-group {
      padding-left: 90px;

      .comment-group {
        padding-left: 50px;
      }
    }
  }
  
  .comment__text {
    padding-right: 55px;
  }
}

@include media-breakpoint-between(lg, lg) {
  .comment-group {
    .comment-group {
      padding-left: 55px;
    }
  }
  .comment__text {
    padding-right: 15px;
  }
}

@include media-breakpoint-up(xl) {
  .comment-group { 
    max-width: 850px;
  }
  
  .comment-group { 
    * + .comment-group { margin-top: 25px; }
  }
  
  .comment-group + .comment-group {
    margin-top: 45px;
  }
}

// Comment box
.comment-box {
  max-width: 830px;
  .unit__body {
    flex-grow: 1;
  }
  
  * + .rd-mailform {
    margin-top: 20px;
  }
}

.comment-box__image {
  border-radius: 50%;
}

* + .comment-box {
  margin-top: 30px;
}

@include media-breakpoint-up(md) {
  * + .comment-box {
    margin-top: 42px;
  }
}
