//
// Card custom styles
// 

// Corporate
//
$accordion-corporate-color: $gray-700;
$accordion-corporate-background: $white;
$accordion-corporate-border: #dedede;
$accordion-corporate-content-color: $body-color;
$accordion-corporate-arrow-color: $white;
$accordion-corporate-arrow-background: $primary;
$accordion-corporate-shadow: -1px 2px 5px 0px rgba(#444953, 0.12);
$accordion-corporate-border-radius: 6px;
$accordion-corporate-header-padding: 21px 82px 21px 32px;
$accordion-corporate-content-padding: 25px 44px 25px nth($accordion-corporate-header-padding, 4);

// Line
//
$accordion-line-color: $gray-700;
$accordion-line-background: $white;
$accordion-line-border: #dedede;
$accordion-line-content-color: $body-color;
$accordion-line-arrow-color: $white;
$accordion-line-arrow-background: $primary;
$accordion-line-border-radius: 6px;
$accordion-line-header-padding: 21px 35px 21px 0;
$accordion-line-content-padding: 25px 0 0 0; 

// Card groups
.card-group-custom {
  margin-bottom: 0;

  .card-header + .collapse > .card-body,
  .card-header + .collapse > .list-group {
    border-top: 0;
  }

  .card + .card {
    margin-top: 0;
  }
}

.card-title {
  margin-bottom: 0;
}

.card-header {
  background-color: transparent;
}

.card-group-custom.card-group-corporate {
  .card + .card {
    margin-top: 30px;
  }
}

.card-custom {
  display: block;
  margin: 0;
  background: inherit;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  text-align: left;

  .card-title a {
    display: block;
  }
  

  // Card heading
  .card-header {
    padding: 0;
    border-bottom: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  // Card contents
  .card-body {
    padding: 0;
    border: 0;
  }
}

* + .card-group-custom {
  margin-top: 35px;

  @include media-breakpoint-up(md) {
    margin-top: 50px;
  }
}

// Card corporate
.card-corporate {
  text-align: left;
  box-shadow: $accordion-corporate-shadow;
  .card-title {
    margin-bottom: 0;
  }

  .card-title a,
  .card-body {
    background: $accordion-corporate-background;
  }

  .card-title a {
    position: relative;
    z-index: 1;
    padding: $accordion-corporate-header-padding;
    font-family: $font-family-sec;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0;
    color: $accordion-corporate-color;
    transition: 1.3s all ease;
    border-radius: $accordion-corporate-border-radius $accordion-corporate-border-radius 0 0;
    border-bottom: 1px solid $accordion-corporate-border;

    // Collapsed state
    &.collapsed {
      .card-arrow {
        &::before {
          opacity: 1;
        }

        &::after {
          opacity: 0;
        }
      }
    }
  }

  // Card arrow
  .card-arrow {
    position: absolute;
    top: 0;
    bottom: -1px;
    right: -1px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70px;
    background: $accordion-corporate-arrow-background;
    border-radius: 0 $accordion-corporate-border-radius 0 0;
    transition: .33s all ease;

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      z-index: 4;
      font-family: 'Material Design Icons';
      font-size: 26px;
      color: $white;
      transition: .22s;
    }

    &::before {
      content: '\f504';
      opacity: 0;
    }

    &::after {
      content: '\f465';
      opacity: 1;
    }
  }

  .collapse {
    position: relative;
    z-index: 1;
    color: $accordion-corporate-content-color;
    border-radius: 0 0 $accordion-corporate-border-radius $accordion-corporate-border-radius;
  }

  .card-body {
    padding: $accordion-corporate-content-padding;
  }

  // Media queries
  @include media-breakpoint-down(md) {
    .card-title a,
    .card-body {
      padding-left: 25px;
    }
  }

  @include media-breakpoint-up(md) {
    .card-title a {
      font-size: 22px;
    }
  }
}

// Card line
.card-group-line {
  counter-reset: card-line;
  
  .card-line {
    .card-title a {
      display: flex;
      align-items: center;
      &::before {
        display: block;
        content: counter(card-line, decimal-leading-zero);
        counter-increment: card-line;
        margin-right: 10px;
        font-size: 1.5em;
        color: $primary;
      }
    }
  }
  
  .card-line + .card-line {
    margin-top: 5px;
  }
  
  @include media-breakpoint-up(xl) {
    .card-line {
      .card-title a {
        &::before {
          margin-right: 15px;
        }
      }
    }
  } 
}

.card-line {
  text-align: left;
  padding: 0 4px;
  
  .card-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.4;
    letter-spacing: 0.025em;
  }
  
  .card-title a {
    position: relative;
    z-index: 1;
    display: block;
    padding: $accordion-line-header-padding;
    color: $accordion-line-color;
    background-color: transparent;
    border-bottom: 1px solid $accordion-line-border;

    // Collapsed state
    &.collapsed {
      .card-arrow {
        &::before {
          opacity: 1;
        }
        
        &::after {
          opacity: 0;
        }
      }
    }
  }

  // Card arrow
  .card-arrow {
    position: absolute;
    top: 0;
    bottom: -1px;
    right: 0;
    width: 22px;
    z-index: 2;
    display: flex;
    align-items: center;
    transition: .33s all ease;

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      z-index: 4;
      transform: translateY(-50%);
      font-family: 'Material Design Icons';
      font-size: 26px;
      color: $accordion-line-arrow-color;
      transition: .22s;
    }

    &::before {
      content: '\f504';
      opacity: 0;
    }

    &::after {
      content: '\f465';
      opacity: 1;
      color: rgba($accordion-line-arrow-color, .3);
    }
  }

  .collapse {
    position: relative;
    z-index: 1;
    color: $accordion-line-content-color;
    border-radius: 0 0 $accordion-line-border-radius $accordion-line-border-radius;
  }

  .card-body {
    padding: $accordion-line-content-padding;
  }
}

.card-line + .card-line {
  margin-top: 20px;
}

%context-dark {
  .card-line {
    .card-title a {
      color: $white;
      border-bottom-color: rgba($white, .2);
    }
    
    .card-arrow {
      color: $white;
      
      &::after {
        color: rgba($white, .3);
      }
    }

    .card-body {
      color: rgba($white, .8);
    }
  }
}

@include media-breakpoint-up(md) {
  .card-line {
    .card-title {
      font-size: 18px;
    }
  } 
}

@include media-breakpoint-up(xl) {
  .card-line {
    .card-title {
      font-size: 24px;
    }
  } 
}
