/*
* Element groups
*/


html {
  .group {
    @include group(15px, 20px);
  } 
  
  .group-sm {
    @include group(10px, 10px);
  }
  
  .group-lg {
    @include group(30px, 20px);
  }
  
  .group-xl {
    @include group(30px, 20px);
    
    @include media-breakpoint-up(lg) {
      @include group-media(45px, 20px);
    }
  }
  
  .group-sm-justify {
    display: flex;
    align-items: center;
    justify-content: center;

    > * {
      flex-grow: 1;
      flex-shrink: 0;
    } 

    @include media-breakpoint-down(xs) {
      .button-icon {
        min-width: 80px;
        font-size: 0;
        padding-left: 8px;
        padding-right: 8px;
        
        .icon {
          padding: 0;
          font-size: 20px;
        }
      } 
    }
    
    @include media-breakpoint-up(sm) {
      .button {
        padding-left: 20px;
        padding-right: 20px;
      } 
    }
    
    @include media-breakpoint-up(md) {
      .button {
        min-width: 130px;
      }
    }
  }

  .group-1 {
    @include group(20px);
    
    @media (min-width: 480px) {
      @include group(30px, 20px);
    }
  }


  // Mods
  .group-middle { 
    display: inline-table;
    vertical-align: middle;
    
    > * {
      vertical-align: middle;
    }
  }
}

