//
// Quote Primary
//
.quote-primary {
}

.quote-primary-mark {
  position: relative;
  top: 6px;
  display: block;
  width: 37px;
  height: 27px;
  fill: $primary;
  flex-shrink: 0;
}

.quote-primary-body {
  position: relative;
  padding: 25px 0;
  border-top: 1px solid $gray-200;

  &::before,
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 13px;
    border-style: solid;
    border-color: $gray-200;
  }

  &::before {
    left: 0;
    width: 78px;
    border-width: 1px 1px 0 0;
  }

  &::after {
    right: 0;
    width: calc(100% - 78px - 12px);
    border-width: 1px 0 0 1px;
    transform-origin: 0 0;
    transform: skew(-45deg);
  }
}

.quote-primary-text {
  padding-top: 20px;
}

.quote-primary-cite {
  font-weight: 700;
}

.quote-primary-footer {
  padding-left: 5px;
}

* + .quote-primary {
  margin-top: 30px;
}

* + .quote-primary-footer {
  margin-top: 8px;
}

@include media-breakpoint-up(sm) {
  .quote-primary-body {
    display: flex; 
    padding: 32px 20px 40px 30px;
  }

  .quote-primary-text {
    padding-top: 0;
    padding-left: 20px;
  }

  .quote-primary-footer {
    padding-left: 30px;
  }
}
 
@include media-breakpoint-up(md) {
  .quote-primary-body {
    padding-right: 40px;
  }
}

// 
// Quote Classic
// 
.quote-classic {
  text-align: left;
}

.quote-classic__text {
  font: italic 300 14px/22px $font-family-sec;
  letter-spacing: .05em;
}

.quote-classic__meta {
  @include spacing(30px, 10px);
  vertical-align: middle;
  font-family: $font-family-sec;
  line-height: 1.2;
  font-weight: 500;
  
  > * {
    display: inline-block;
    position: relative;
  }
  
  > *:not(:last-child) {
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate3d(0, -50%, 0);
      height: 19px;
      width: 1px;
      background: $gray-300;
      pointer-events: none;
    }
  }
}

.quote-classic__cite {
  font-size: 17px;
  color: $gray-800; 
}

.quote-classic__info {
  font-size: 15px;
  color: $primary;
}

* + .quote-classic { margin-top: 30px; }
* + .quote-classic__meta { margin-top: 20px; }
.quote-classic + .quote-primary { margin-top: 65px; }

// Medium ≥768px
@include media-breakpoint-up(md) {
  .quote-classic__text {
    font-size: 18px;
    line-height: (26 / 18);
  }

  .quote-classic__cite {
    font-size: 20px;
  }
  
  .quote-classic__info {
    font-size: 17px;
  }

  * + .quote-classic__meta { margin-top: 25px; }
}

//
// Quote Light
//
.quote-light {
  display: flex;
  @include groupX(20px);
  text-align: left;
}

.quote-light__mark {
  position: relative;
  top: 2px;
  flex-shrink: 0;
  fill: $color-5;
}

.quote-light__text {
  
}

* + .quote-light { margin-top: 20px; }

@include media-breakpoint-up(md) {
  * + .quote-light { margin-top: 30px; } 
}


//
// Quote Line
//
.quote-line {
  position: relative;
  padding-left: 25px;
  font: italic 100 16px $font-family-sec;
  line-height: 1.4;
  letter-spacing: .025em;
  text-align: left;
  
  &::before {
    content: '';
    position: absolute;
    left: 5px;
    top: .75em;
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 1px;
    background: $color-4;
  }
}

* + .quote-line { margin-top: 20px; }

@include media-breakpoint-up(sm) {
  .quote-line {
    padding-left: 55px;
    font-size: 18px;

    &::before {
      top: .65em;
      width: 40px;
    }
  }
}

@include media-breakpoint-up(md) {
  .quote-line {
    font-size: 20px;
    line-height: 1.2;
  }

  .quote-line + .quote-classic { margin-top: 65px; }
}

@include media-breakpoint-up(xl) {
  .quote-line {
    padding-left: 120px;
    font-size: 24px;

    &::before {
      top: .55em;
      width: 85px;
    }
  }

  * + .quote-line { margin-top: 40px; }
}
