/*
*
* Profiles
*/
//
// Table of contents
//
// Profile Minimal
// Profile Modern
//


//
// Profile Minimal
//
.profile-minimal {
  display: flex;
  align-items: center;
  @include group(15px);
  
}

.profile-minimal__image {
  display: block;
  max-width: 75px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.profile-minimal__name,
.profile-minimal__position {
  font-family: $font-family-sec;
  font-size: 17px;
  line-height: 1.2;
}

.profile-minimal__position {
  color: $primary;
}

.profile-minimal__list {
  font-size: 17px;
  @include spacing(10px);

  li {
    display: inline-block;
  }

  a.icon {
    color: $gray-800;

    &:hover {
      color: $primary;
    }
  }
}

* + .profile-minimal__position { margin-top: 5px; }
* + .profile-minimal__list { margin-top: 10px; }


// Medium ≥576px
@include media-breakpoint-up(sm) {
  .profile-minimal {
    display: block;
    text-align: center;
  }

  .profile-minimal__image {
    max-width: 100px;
  }
}

// Profile Modern
.profile-modern {
  padding: 0 4px;
  @include groupY(30px);
  text-align: left;

  &:hover {
    .profile-modern__figure {
      border-color: $white;
      box-shadow: $shadow-area-1;

      img {
        filter: grayscale(0);
      }
    }
  }
}

.profile-modern__figure {
  width: 100%;
  min-width: 200px;
  border: 10px solid $gray-300;
  transition: 0.35s all cubic-bezier(0.59, 0.3, 0.68, 1.16);

  img {
    width: 100%;
    filter: grayscale(100%);
    transition: inherit;
  }
}

.profile-modern__name {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: .6em;
    right: 100%;
    display: inline-block;
    line-height: inherit;
    margin-right: .55em;
    vertical-align: middle;
    width: 100px;
    border-bottom: 1px solid $gray-200;
  }
}

.profile-modern__position {
  color: $primary;
}

.profile-modern__inset {
  position: relative;
  overflow: hidden;
  padding-left: 35px;
}

* + .profile-modern__position { margin-top: 5px;}
* + .profile-modern__inset { margin-top: 15px; }
.profile-modern__inset + * { margin-top: 15px; }

@include media-breakpoint-down(xs) {
  .profile-modern {
    max-width: 340px;
    margin-left: auto;
    margin-right: auto;
  }
}

@include media-breakpoint-up(sm) {
  .profile-modern__name {
    &::before {
      margin-right: .75em;
    }
  }
}

@include media-breakpoint-between(sm, md) {
  .profile-modern {
    display: flex;
    align-items: flex-start;
    @include groupX(30px);
  }

  .profile-minimal__figure {
    flex-shrink: 0;
  }

  .profile-minimal__main {
    flex-grow: 1;
  }

  .profile-modern__inset {
    padding-left: 0;
  }
}

@include media-breakpoint-up(lg) {
  .profile-modern__inset {
    padding-left: 50px;
  }

  * + .profile-modern__inset { margin-top: 27px; }
}

@include media-breakpoint-up(xl) {
  .profile-modern__figure {
    border-width: 17px;
  }

  .profile-modern__inset {
    padding-left: 70px;
  }
}

//
// Profile Light
//
.profile-light {
  @include groupY(16px);
  text-align: center;
}

.profile-light__figure {
  flex-shrink: 0;
  
  img {
    max-width: 200px;
  }
}

.profile-light__main {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  padding: 30px 25px;
  background: $gray-100;
  text-align: left;
}


.profile-light__title { }

.profile-light__text {
  font-family:  $font-family-sec;
  font-size: 14px;
  color: $gray-800;
}

* + .profile-light__text { margin-top: 15px; }

@include media-breakpoint-down(xs) {
  .profile-light {
    max-width: 340px;
    margin-left: auto;
    margin-right: auto;
  } 
}

@include media-breakpoint-up(sm) {
  .profile-light {
    display: flex;
    @include groupX(16px);  
  }
}

@include media-breakpoint-up(md) {
  .profile-light__inner {
    position: relative;
    overflow: hidden;
    padding: 0 0 0 50px;
  }

  .profile-light__title {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: .6em;
      right: 100%;
      display: inline-block;
      line-height: inherit;
      margin-right: .55em;
      vertical-align: middle;
      width: 120px;
      border-bottom: 1px solid $color-4;
    }
  }
  
  .profile-light__text {
    font-size: 16px;
  }
}

@include media-breakpoint-up(xl) {
  .profile-light__main {
    padding: 35px 30px 25px 70px;
  }
  
  .profile-light__inner {
    padding-left: 90px;
    max-width: 750px;
  }

  .profile-light__text {
    font-size: 17px;
  }
} 
