/*
*
* Icons
*/

//
// Base styles
//
.icon {
  display: inline-block;
  font-size: 16px;
  line-height: 1;
 
  &::before {
    position: relative;
    display: inline-block; 
    font-weight: 400;
    font-style: normal;
    speak: none; 
    text-transform: none;
  }
}

//
// Alternate icons
//

.icon-default {
}

.icon-gray-500 {
  color: $gray-500;
}

.icon-primary {
  color: $primary;
}

//
// Icon Shapes
//
.icon-circle {
  border-radius: 50%;
}

//
// Icon Sizes
//
.icon-xs {
}

.icon-sm {
}

.icon-md {
  font-size: 21px;

  &.mdi-youtube-play {
    font-size: 25px;
  }
}

.icon-lg {
  font-size: 28px;
}
