/*
*
* ToTop
*/

.ui-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: $zindex-to-top;
  width: $form-input-height;
  height: $form-input-height;
  font-size: 20px;
  line-height: $form-input-height - 4px;
  color: $white;
  background: $primary;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  transition: .45s ease-in-out;
  transform: translate3d(0, 100px, 0);
  border-radius: 50%;

  &:hover {
    &::after {
      transform: translate3d(0, 0, 0);
    }
  }

  &:hover {
    color: $white;
    background: $gray-800;
    text-decoration: none;
  }

  &:focus {
    color: $white;
  }

  &.active {
    transform: translate3d(0, 0, 0);
  }
}

@include media-breakpoint-up(sm) {
  .ui-to-top {
    right: 40px;
    bottom: 40px;
  }
}


