/*
*
* Circle Progress Bars
*/

.progress-bar-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  line-height: 1.2;

  canvas {
    vertical-align: middle;
  }

  span {
    position: absolute;
    top: 50%;
    left: 52%;
    font-family: $font-family-sec;
    font-size: 36px;
    line-height: 36px;
    font-weight: 700;
    transform: translate(-50%, -50%);
    color: $gray-700;

    &::after {
      content: '\0025';
    }
  }
}

.progress-bar-circle-title {
  font-size: 15px;
  line-height: 1.3;
  color: $gray-600;
}

* + .progress-bar-circle-title { margin-top: 12px; }

@include media-breakpoint-up(sm) {
  .progreess-bar-circle-title {
    font-size: 17px;
  }
}