/*
*
* Preloader
*/

//
// Base styles
//
.preloader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 9999999;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 20px 6%;
  background: $white;
  transition: .15s all linear;

  &.loaded {
    opacity: 0;
    visibility: hidden;
  }
}

.preloader-body {
  text-align: center;
}

//
// Custom Styles
//
$duration: 1500ms; 
$timing: linear;

.preloader-item {
  position: relative;
  width: 30px; 
  height: 120px;
  display: flex;
  flex-flow: column nowrap;
  
  .diamond {
    position: absolute;
    left: 0; right: 0; bottom: 0;
    margin: 0 auto;
    width: 30px; height: 30px;
    border-radius: 2px;
    background: $primary;
    transform: translateY(0) rotate(45deg) scale(0);
    animation: diamonds $duration $timing infinite;
    
    @for $i from 1 through 4 {
      &:nth-child(#{$i}) {
        animation-delay: -($duration / 1.5) * $i;
      }
    }
  }
}

@keyframes diamonds {
  50% {
    transform: translateY(-50px) rotate(45deg) scale(1);
  }
  100% {
    transform: translateY(-100px) rotate(45deg) scale(0);
  }
}
