/*
*
* Thumbnails
*/

// 
// Table of Contents
//
// Base styles
// Figure Light
// Thumbnail video
// Thumbnail Light
//


// Base styles
.video-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center center;
  background-size: cover;
  cursor: pointer;
}

.image-wrap {
  img {
    width: 100%;
  }
}

//
// Figure Light
//
.figure-light figcaption {
  padding-top: 10px;
  color: $gray-400;
}

* + .figure-light { margin-top: 30px; }

//
// Thumbnail video
//
.thumbnail-video {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    display: block;
    width: 100%;
    pointer-events: none;
    padding-top: (598 / 961) * 100%;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to top, rgb(27, 34, 59) 0%, rgba(54, 64, 83, 0) 100%);
    opacity: 0.678;
    pointer-events: none;
  }

  .button-video {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
}

.thumbnail-video__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 100%;
  min-width: 100%;
  width: auto;
  height: auto;
  max-width: none;

  @supports (object-fit: cover) {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    transform: none;
    object-fit: cover;
    object-position: center center;
  }
}

// Thumbnail video 2
.thumbnail-video-outer { }

.thumbnail-video-1 {
  position: relative;
  text-align: center;

  .embed-responsive {
    &::before {
      padding-top: (503 / 1171) * 100%;
      min-height: 300px;
    }
  }

  .video-overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    z-index: 1;
    background-position: 80% center;

    h6,
    .heading-6 {
      color: rgba($white, .7);
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-image: linear-gradient(to top, rgb(27,34,59) 0%, rgba(54,64,83,0) 100%);
      opacity: 0.851;
      z-index: -1;
    }
  }

  * + .button-video { margin-top: 25px; }
}

//
// Image Groups
//

// 1
.image-group-1 {
  img {
    max-width: 80%;
    
    &:nth-child(odd) {
      float: left;
    }

    &:nth-child(even) {
      float: right;
    }
  }

  &::after {
    content: '';
    display: block;
    clear: both;
  }

  img + img {
    margin-top: -46%;
  }
}

@include media-breakpoint-up(lg) {
  .image-group-1 {
    padding-right: 15px;
  }
}

@include media-breakpoint-up(xl) {
  .image-group-1 {
    padding-right: 40px;
  }
}


//
// Thumbnail Light
//
.thumbnail-light {
  position: relative;
  display: block;
  
  &::before,
  &::after {
    opacity: 0;
    visibility: hidden;
  }
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba($gray-700, .15);
    pointer-events: none;
    transition: .55s;
  }
  
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0) scale3d(1.03, 1.03, 1.03);
    content: '\ec22';
    font-family: 'Linearicons';
    font-size: 30px;
    color: $white;
    transition: .33s;
  }
  
  img {
    display: block;
    width: 100%;
  }
  
  &:hover {
    &::before,
    &::after {
      opacity: 1;
      visibility: visible;
    }
    
    &::after {
      
    }
  }
}
