/*
*
* Backgrounds
*/


%context-dark {
  &,
  #{headings()} {
    color: $white;
  }

  h6,
  .heading-6 {
    color: rgba($white, .2);
  }
  
  hr {
    border-color: rgba($white, .13);
  }
 
  // Logo
  .brand {
    .brand-logo-dark { display: none; }
    .brand-logo-light { display: block; }
  }
  
  // Buttons
  .button-default-outline {
    @include btn-variant($white, transparent, $white, $white, $primary, $primary);
  }

  .button-primary {
    &:hover {
      color: $gray-800;
      background: $white;
      border-color: $white;
    }
  }
}

%context-light {
  color: $body-color;

  #{headings()}{
    color: $headings-color;
  }

  // Logo
  .brand {
    .brand-logo-dark { display: block; }
    .brand-logo-light { display: none; }
  }
}

.context-dark {
  @extend %context-dark;
}


/**
* Light Backgrounds
*/
.bg-default {
  @include bg-behaviour($body-bg); 
}

.bg-gray-100 {
  @include bg-behaviour($gray-100);
}


/**
* Dark Backgrounds
*/
.bg-gray-700 {
  @extend %context-dark;
  @include bg-behaviour($gray-700);
  color: rgba($white, .5);

  .lead {
    color: $white;
  }

  .box-lily__title {
    color: $white;
  }
  
  // Buttons
  .button-primary {
    &::before {
      background: $primary-darken;
    }

    &:hover {
      color: $white;
      border-color: $primary-darken;
      background: $primary-darken;
    }
  }
}

.parallax-container.bg-gray-700 {
  color: $white;
}


/** 
* Accent Backgrounds
*/
.bg-primary {
  @extend %context-dark;
  @include bg-behaviour($primary);
}


/**
* Background Image
*/
.bg-image {
  
}

[class*='bg-'] {
  background-size: cover;
  background-position: center center;
}

// Desktop only
//
html:not(.tablet):not(.mobile) {
  .bg-fixed {
    @include media-breakpoint-up(lg) {
      background-attachment: fixed;
    }
  }
}
