/*
*
* Utilities custom
*/
.height-fill {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  > * {
    flex-grow: 1;
    flex-shrink: 0;
  }
}

// Object inline 
.object-inline {
  white-space: nowrap;
  > * {
    display: inline-block;
    min-width: 20px;
    vertical-align: top;
    white-space: normal;
  }

  > * + * { margin-left: 5px; }
}

.oh {
  position: relative;
  overflow: hidden;
}

.text-block {
  > * {
    margin-left: .125em;
    margin-right: .125em;
  }
}

// BTC to USD
.btc-to-usd {
  position: relative;
  min-width: 100px;

  > * + * { margin-top: 2px; }
}

.btc-to-usd__title {
  text-transform: uppercase;
  letter-spacing: .075em;
}

.btc-price {
  position: relative;
  display: inline-block;
  color: $gray-800;
  white-space: nowrap;

  .btcwdgt {
    min-width: 120px !important;
    .btcwdgt-footer,
    .btcwdgt-edge {
      display: none !important;
    }

    .btcwdgt-body {
      padding: 0 0 0 0 !important;
      margin: 0 !important;
      height: auto !important;
    }

    .btcwdgt-body ul {
      overflow: hidden !important;
      margin: 0 !important;
      font-size: 16px !important;
      height: 1em !important;
    } 

    .btcwdgt-body ul li {
      font-family: $font-family-trd !important;
      font-size: 16px !important;
      font-weight: 700 !important;
      min-height: 1.3em;
      line-height: 1.2em !important;
    }

    .btcwdgt-body span {
      display: none !important;
    }

    &.btcwdgt-s-price {
      .arrow {
        position: static !important;
        display: inline-block !important;
        margin: 0 0 0 5px !important;

        &:not(.up):not(.down) {
          opacity: 0 !important;
        }

        &.up {
          border-bottom-color: $success-color !important;
        }

        &.down {
          border-top-color: $danger-color !important;
        }
      }
    }

    ul li {
      &::before {
        content: '\0024';
      }
    }
  }

  .btcwdgt-text-ticker.btcwdgt-light {
    background-color: transparent !important;
    box-shadow: none !important;
    margin: 0 !important;
  }
}

//
// Mobile App Frame
//
.mobile-app-frame {
  position: relative;
  right: -25px; 
  z-index: 0;
  overflow: hidden;
  max-width: 443px;
  margin-left: auto;
  margin-right: auto;
}

.mobile-app-frame__content {
  position: absolute;
  overflow: hidden;
  top: 12.8%;
  left: 5.9%;
  right: 18.1%;
  bottom: 0;

  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    min-height: 100%;
    min-width: 100%;
    width: auto;
    height: auto;
    max-width: none;

    @supports (object-fit: cover) {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform: none;
      object-fit: cover;
      object-position: center top;
    }
  }
}

@include media-breakpoint-up(sm) {
  .mobile-app-frame {
    right: 0;
  }
}

@include media-breakpoint-up(xl) {
  .mobile-app-frame {
    margin-left: 30px;
  }
} 


@include media-breakpoint-between(md, md) {
  .mobile-app-frame {
    margin-left: -185px;
    right: -30px;
  }
}
